import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import {
  ShipViaCodeConfigurationModel,
  ShipViaCodeDetail,
  ShipviaCode,
  ShipviaCodeDetailDTO,
} from "src/app/models/shipviaCodeConfiguration.model";
import { DataService } from "src/app/services/data.service";
import { ProcessShipperService } from "src/app/services/process-shipper.service";
import { shipViaCodeConfigurationService } from "src/app/services/ship-via-code-configuration.service";
import {
  CarrierServiceDTO,
  PackageTypeDTO,
} from "src/app/functional-components/models/default-data";

@Component({
  selector: "app-create-ship-via-code-configuration",
  templateUrl: "./create-ship-via-code-configuration.component.html",
  styleUrls: ["./create-ship-via-code-configuration.component.scss"],
})
export class CreateShipViaCodeConfigurationComponent implements OnInit {
  shipviaCodeDetailDTO: ShipviaCodeDetailDTO = {
    shipviaCode: {},
    shipviaCodeSpecialDetail: {},
    shipViaCodeDetail: {
      id: 0,
      xcarrierCodesId: 0,
      freightType: 0,
      billable: false,
      includeHandlingCharges: false,
      billHandlingChargesOnly: false,
      flatFeeBillableOverride: 0,
      minimumCalculatedFreight: 0,
      maximumCalculatedFreight: 0,
      minimumBillableFreight: 0,
      maximumBillableFreight: 0,
      defaultThirdPartyRecipientId: 0,
    },
  };
  plants: any;
  carrier: any;
  erpData: any;
  carrierCodes: PackageTypeDTO[];
  paymentType: any[] = [];
  carrierService: CarrierServiceDTO[];
  frieghtType: any;
  itemSectionExpanded = false;
  additionalItemExpanded = false;
  shipViaDetailExpanded = false;
  specialServiceExpanded = false;
  packageType: PackageTypeDTO[];
  signatureOption: any[] = [];
  id: any;
  breadcrumb: string;

  constructor(
    private svcService: shipViaCodeConfigurationService,
    private processShipperService: ProcessShipperService,
    private dataService: DataService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    console.log("plant", this.dataService.selectedPlantId);
    this.dataService.plantChangeObs.subscribe((selectedPlant) => {
      console.log("plantchange", selectedPlant);
    });

    this.processShipperService.getDefinationValue("RTNG").subscribe((data) => {
      this.frieghtType = data;
      console.log("freigt", this.frieghtType);
    });
    this.svcService.getSignatureOption().subscribe((data) => {
      data.forEach((element) => {
        this.signatureOption.push({ value: element, label: element });
      });
    });
    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;
    });
    this.svcService.getPlants().subscribe((plants) => {
      this.plants = plants;
      if (this.id > 0) {
        this.breadcrumb = "Update";
        this.svcService.getShipViaCodeData(this.id).subscribe((data) => {
          console.log("svcData", data);
          this.shipviaCodeDetailDTO = data;
          this.shipviaCodeDetailDTO.shipviaCode.plantid = this.plants.filter(
            (x) => x.plantId == this.shipviaCodeDetailDTO.shipviaCode.plantid
          )[0].id;
          this.plantChange();
          this.carrierCodeChange();
        });
      }
      console.log("plant", this.plants);
    });
  }

  plantChange() {
    this.svcService
      .getPlantCarrier(this.shipviaCodeDetailDTO.shipviaCode.plantid)
      .subscribe((data) => {
        this.carrier = data;
        console.log("carrier", data);
      });
    const platId = this.plants.filter(
      (x) => x.id == this.shipviaCodeDetailDTO.shipviaCode.plantid
    )[0].plantId;
    this.svcService.getPlantERP(platId).subscribe((data) => {
      this.erpData = data;
      console.log("erp", data);
    });
  }

  carrierCodeChange() {
    this.processShipperService
      .getPackageType(this.shipviaCodeDetailDTO.shipviaCode.carrier)
      .subscribe((data) => {
        this.packageType = data;
        console.log("package", data);
      });

    this.processShipperService
      .getPaymentTypes(this.shipviaCodeDetailDTO.shipviaCode.carrier)
      .subscribe((data) => {
        data.forEach((element) => {
          this.paymentType.push({ name: element, value: element });
        });
        console.log("payment", data);
      });

    this.processShipperService
      .getCarrierService(
        this.shipviaCodeDetailDTO.shipviaCode.carrier,
        parseInt(this.shipviaCodeDetailDTO.shipviaCode.plantid)
      )
      .subscribe((data) => {
        this.carrierService = data;
        console.log("carrierServ", data);
      });
  }
  back() {}
  save() {
    this.shipviaCodeDetailDTO.shipviaCode.plantid = this.plants.filter(
      (x) => x.id == this.shipviaCodeDetailDTO.shipviaCode.plantid
    )[0].plantId;
    this.shipviaCodeDetailDTO.shipviaCode.packageType =
      this.shipviaCodeDetailDTO.shipviaCode.packageType.toString();
    this.svcService.save(this.shipviaCodeDetailDTO).subscribe((data) => {
      console.log(data);
    });
  }
}
