import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";

@Component({
  selector: "app-handling-charges",
  templateUrl: "./handling-charges.component.html",
  styleUrls: ["./handling-charges.component.scss"],
})
export class HandlingChargesComponent implements OnInit {
  svcService: any;
  data: any;
  constructor(private router: Router) {}

  administrationList = false;
  batchProcessList = false;
  configurationList = true;
  ERPSetupList = false;
  hazardousList = false;
  carriersIntegrationList = false;
  carriersNonIntegrationList = false;
  toolKit = false;

  showAddHandlingPopup = false;
  columns = [
    "Quick Actions",
    "Handling Charge Type",
    "Handling Charge",
    "Start Range",
    "End Range",
    "Apply To Each Box",
  ];

  ngOnInit(): void {
    this.svcService.get().subscribe((data) => {
      this.data = data;
    });
  }
  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
  create() {
    this.router.navigate(["create-address-master"]);
  }
}
