import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { eFlowClient } from "src/app/models/eflowClient.model";
import { DataService } from "src/app/services/data.service";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-create-eflow-configuration",
  templateUrl: "./create-eflow-configuration.component.html",
  styleUrls: ["./create-eflow-configuration.component.scss"],
})
export class CreateEflowConfigurationComponent implements OnInit {
  breadcrumb = "New";
  id: number;
  eflowData: eFlowClient = {};
  clientEflows = [
    { textField: "Local", valueField: 1 },
    { textField: "Remote", valueField: 2 },
  ];
  constructor(
    private activatedRoute: ActivatedRoute,
    private processShipperService: ProcessShipperService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;

      if (this.id > 0) {
        this.breadcrumb = "Update";
        this.processShipperService.getEflowClient(this.id).subscribe((data) => {
          this.eflowData = data;
        });
      }
    });
  }

  back() {
    this.router.navigate(["/eflow-configuration/"]);
  }
  save() {
    if (this.id > 0) {
      this.eflowData.clientName = this.eflowData.clientId;
      // this.processShipperService
      //   .unsubscribeEflowClient(this.eflowData)
      //   .subscribe((data) => {
      //     this.eflowData = data;
      //     this.back();
      //   });
    } else {
      this.processShipperService
        .saveEflowClient(this.eflowData)
        .subscribe((data) => {
          this.eflowData = data;
          this.back();
        });
    }
  }
}
