import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { DataService } from "src/app/services/data.service";
import { configurationService } from "src/app/services/configuration-service";
import { packingListConfigurationModel } from "src/app/models/packingListConfiguration.model";
@Component({
  selector: "app-packing-list",
  templateUrl: "./packing-list.component.html",
  styleUrls: ["./packing-list.component.scss"],
})
export class PackingListComponent implements OnInit {
  administrationList = false;
  batchProcessList = false;
  configurationList = true;
  ERPSetupList = false;
  hazardousList = false;
  carriersIntegrationList = false;
  carriersNonIntegrationList = false;
  toolKit = false;
  dominonDetails = true;

  showAddHandlingPopup = false;
  columns = [
    "Quick Actions",
    "Plant ID",
    "Print Ordered Qty",
    "Header Text",
    "Footer Text",
    "PrintFrmAdd",
    "PrintPkgDetails",
  ];
  data: packingListConfigurationModel[];

  constructor(
    private configurationService: configurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.configurationService
      .getPackingListConfiguration()
      .subscribe((data) => {
        this.data = data;
      });
  }
  edit(id) {
    this.router.navigate(["createPackagingList/" + id]);
  }
  create() {
    this.router.navigate(["createPackagingList"]);
  }
}
