import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { configurationService } from "src/app/services/configuration-service";
import { DataService } from "src/app/services/data.service";
import { ExcelService } from "src/app/services/excel.service";

@Component({
  selector: "app-log-viewer",
  templateUrl: "./log-viewer.component.html",
  styleUrls: ["./log-viewer.component.scss"],
})
export class LogViewerComponent implements OnInit {
  columns = [
    "",
    "DateRange",
    "ID",
    "Plant",
    "Severity",
    "Interface",
    "Event",
    "DeliveryDocNumber",
    "Description",
    "ErrorCode",
    "Error",
    "Time",
    "UI",
  ];
  data: any;
  selectedLogs: any;
  rangeDates: any;
  filteredData: any;
  fromDate: any;
  toDate: any;
  constructor(
    private svcService: configurationService,
    private dataService: DataService,
    private router: Router,
    private excelService: ExcelService
  ) {}

  ngOnInit(): void {
    this.fromDate = new Date();
    this.toDate = new Date();
    this.svcService
      .getLogs(
        this.dateToISOLikeButLocal(this.fromDate),
        this.dateToISOLikeButLocal(this.toDate)
      )
      .subscribe((data: any) => {
        this.data = data;
      });
  }
  edit(id: string) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
  onFilter(event, data) {
    console.log(event);
    console.log(data);
    this.filteredData = data.filteredValue;
    //this.excelService.exportAsExcelFile(data.filteredValue, "test");
  }

  export(type) {
    if (type == "selected") {
      this.excelService.exportAsExcelFile(this.selectedLogs, "selected");
    } else {
      this.excelService.exportAsExcelFile(this.data, "all");
    }
  }
  apply() {
    this.svcService
      .getLogs(
        this.dateToISOLikeButLocal(this.fromDate),
        this.dateToISOLikeButLocal(this.toDate)
      )
      .subscribe((data: any) => {
        this.data = data;
      });
  }
  clear() {}

  dateToISOLikeButLocal(date) {
    const offsetMs = date.getTimezoneOffset() * 60 * 1000;
    const msLocal = date.getTime() - offsetMs;
    const dateLocal = new Date(msLocal);
    const iso = dateLocal.toISOString();
    const isoLocal = iso.slice(0, 19);
    return isoLocal;
  }
}
