import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { batchConfigurationDTOModel } from "src/app/models/batchConfiguration.model";
import { BatchConfigurationService } from "src/app/services/batch-configuration.service";
import { DataService } from "src/app/services/data.service";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-batch-configuration",
  templateUrl: "./batch-configuration.component.html",
  styleUrls: ["./batch-configuration.component.scss"],
})
export class BatchConfigurationComponent implements OnInit {
  columns = [
    "Quick Actions",
    "Batch Template Name",
    "User Id",
    "File Format",
    "Port Number",
    "Item Break Quantity",
  ];
  data: batchConfigurationDTOModel[] = [];
  constructor(
    private bcService: BatchConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.bcService.get().subscribe((data) => {
      this.data = data;
    });
  }

  create() {
    this.router.navigate(["create-batch-configuration"]);
  }

  edit(id) {
    this.router.navigate(["create-batch-configuration/" + id]);
  }
}
