import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ShipViaCodeConfigurationModel } from "src/app/models/shipviaCodeConfiguration.model";
import { DataService } from "src/app/services/data.service";
import { shipViaCodeConfigurationService } from "src/app/services/ship-via-code-configuration.service";

@Component({
  selector: "app-ship-via-code-configuration",
  templateUrl: "./ship-via-code-configuration.component.html",
  styleUrls: ["./ship-via-code-configuration.component.scss"],
})
export class ShipViaCodeConfigurationComponent implements OnInit {
  columns = [
    "Quick Actions",
    "Ship Via Code",
    "Carrier",
    "Service",
    "Payment",
    "Package Type",
    "Account No",
    "Plant ID",
    "ERP",
  ];
  data: ShipViaCodeConfigurationModel[] = [];
  constructor(
    private svcService: shipViaCodeConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.svcService.get().subscribe((data) => {
      this.data = data;
    });
  }
  create() {
    this.router.navigate(["create-ship-via-code-configuration"]);
  }

  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
}
