import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import {
  EntityContainer,
  LstLineItem,
  LstPackageGroup,
  ObjInternationalDetails,
} from "src/app/functional-components/process-shipping/process-shipping.model";

@Component({
  selector: "app-fast-pack-popup",
  templateUrl: "./fast-pack-popup.component.html",
  styleUrls: ["./fast-pack-popup.component.scss"],
})
export class FastPackPopupComponent implements OnInit {
  @Input() itemData: any;
  @Input() dimentionList: any;
  @Output() newItemEvent = new EventEmitter<EntityContainer[]>();
  noOfPackage: number;
  weight: number;
  dimention: string;
  packageList: EntityContainer[] = [];
  columns = [
    "Item Number",
    "Item Description",
    "Total Quantity",
    "Balance Quantity",
    "No of Units per Container",
  ];
  constructor() {}

  ngOnInit(): void {}

  fastPack() {
    for (let index = 0; index < this.noOfPackage; index++) {
      let newPackage: EntityContainer = {} as EntityContainer;
      newPackage.sno = 1;
      newPackage.isprepacked = false;
      newPackage.intHandlingUnitID = 0;
      newPackage.strTrackingNumber = "";
      newPackage.dblTotalWeight = this.weight; //this.packageList[0].dblTotalWeight;
      newPackage.lstPackageGroup = [];

      let packageGroup = {} as LstPackageGroup;
      packageGroup.lstLineItem = [];
      this.itemData.forEach((element) => {
        let packageItem = {} as LstLineItem;
        packageItem.strItemNumber = element.strItemNumber;
        packageItem.balanceQuantity =
          element.intQuantity - this.noOfPackage * element.noOfUnitPerContainer;
        packageItem.intTotalItemQuantity = element.intQuantity;
        packageItem.strItemDescription = element.strDescription;
        packageItem.intOrderedQuantity = element.noOfUnitPerContainer;
        packageItem.objInternationalDetails = {} as ObjInternationalDetails;

        packageGroup.lstLineItem.push(packageItem);
      });
      newPackage.lstPackageGroup.push(packageGroup);
      this.packageList.push(newPackage);
    }
    this.newItemEvent.emit(this.packageList);
  }
}
