  getPlantMasterData() {
    return this.http.get<PlantDTO[]>(
      this.gs.baseURL + "api/MasterData/GetPlants"
    );
  }
  getDefaultData() {
    return this.http.get<UserDefaultDataDTO>(
      this.gs.baseURL + "api/ProcessShipper/GetDefaultData"
    );
  }
  getERPData(senderId) {
    return this.http.get<ErpDTO>(this.gs.baseURL + "GetERPData/" + senderId);
  }

  dateToISOLikeButLocal(date) {
    const offsetMs = date.getTimezoneOffset() * 60 * 1000;
    const msLocal = date.getTime() - offsetMs;
    const dateLocal = new Date(msLocal);
    const iso = dateLocal.toISOString();
    const isoLocal = iso.slice(0, 19);
    return isoLocal;
  }
