import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { forkJoin } from 'rxjs';
import { DataService } from 'src/app/services/data.service';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';
import { EntityContainer, InterfaceLineItemDetails, LstLineItem, LstPackageGroup, ObjInternationalDetails } from '../process-shipping.model';
import { EntityLineItemInternationalDetails, ExportInvoice, InternationalEntity, InternationalShipDetailForm, InternationalShipDetailFormData, InvoiceCopy } from './international-form.model';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MenuItem } from 'primeng/api';
import { element } from 'protractor';
import { Console } from 'console';
import { data } from 'jquery';
import { Dropdown } from 'primeng/dropdown';


@Component({
  selector: 'app-international-form',
  templateUrl: './international-form.component.html',
  styleUrls: ['./international-form.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class InternationalFormComponent implements OnInit, OnChanges {

  @Input() containerData: EntityContainer[];
  @Input() internationalItems: LstLineItem[];
  @Output() internationalLstItems = new EventEmitter();
  @Output() closePopup = new EventEmitter();
  InternationalShipDetailFormData: InternationalShipDetailForm[];
  InternationalData: InternationalShipDetailFormData = {};
  FormData: FormGroup;
  checkBoxChange: string = 'No';
  @Input() packingOn: boolean;
  internationItemDetail: LstLineItem = {} as LstLineItem;
  IsEditAll: boolean = false;
  internationalFormData:any={} as any;
  // product:any;EntityLineItemInternationalDetails
  internationalEntity:InternationalEntity ={} as InternationalEntity;
  visible = true;
  UOMList: any = [];
  LicenceList: any = [];
  OriginCriterion: any = [];
  showItemDetails = true;
  entityLineItemInternationalDetails :EntityLineItemInternationalDetails={} as EntityLineItemInternationalDetails;
  changeSizing: any = "left";
  justifyOptions: any[] = [
    { icon: 'fa-solid fa-list', justify: 'Left', value: 'left' },
    { icon: 'fa-solid fa-grid', justify: 'Right', value: 'right' }

  ];

  columns = [
    "Item",
    "Item Description",
    "Quantity",
    "Declare/Customs Value",
    "Weight",
    "HarmonizedCode",
  ];


  constructor(private processShipperService: ProcessShipperService, private fb: FormBuilder,
    private dataService: DataService,
    private cd: ChangeDetectorRef) { }

  ngOnInit(): void {

    this.internationalItems.map(row => {
      row.IsEditable = false;
    });
  }


  ngOnChanges(changes: SimpleChanges): void {




    this.processShipperService.getInternationalShipDetailForm("2", "UPS").then(
      (data) => {
        this.InternationalShipDetailFormData = data;
        this.InternationalShipDetailFormData.forEach(w => w.selectedValue = "No");

        // if(!changes.InternationalShipDetailFormData.firstChange){
        this.createForm(this.InternationalShipDetailFormData);
        //}

      }
    ).catch((error) => {
      console.log(error);
    });



    //if (!changes.InternationalShipDetailFormData.firstChange) {


    // }
    // console.log('containerData',this.containerData);
    // this.internationItemDetail={objInternationalDetails:{}} as LstLineItem;
    const uomPromise = this.processShipperService.getUOMList();
    const licenePromise = this.processShipperService.getLicenceMasterData();
    const OCPromise = this.processShipperService.getOriginCriterion();
    // forkJoin() to make parallel calls
    forkJoin([uomPromise, licenePromise, OCPromise]).subscribe(responses => {
      let uomList = responses[0];
      let licences = responses[1];
      let originCriterion = responses[2];

      this.UOMList = uomList
        .map(x => { return { textField: x.description, valueField: x.description } as any });
      this.LicenceList = licences
        .map(x => { return { textField: x.description, valueField: x.description } as any });
      this.OriginCriterion = originCriterion
        .map(x => { return { textField: x.description, valueField: x.description } as any });
    });



    // this.createForm(this.InternationalShipDetailFormData);

    if (changes['internationalItems']?.currentValue) {


    }


    // if (changes['containerData']?.currentValue) {
    //   this.lstPackageGroup = [];

    //   changes['containerData']?.currentValue.forEach(element => {

    //     element.lstPackageGroup[0].lstLineItem.reduce((acc, item) => {

    //       let accItem = this.lstPackageGroup?.find(ai => ai.strItemDescription === item.strItemDescription)

    //       if (accItem) {
    //         accItem.intOrderedQuantity += item.intOrderedQuantity
    //       } else {
    //         this.lstPackageGroup.push(item)
    //       }

    //       return acc;
    //     }, [])

    //   });
    // }
    // console.log(this.lstPackageGroup);
    if (this.internationalItems?.length > 0) {
   
      this.selectCard(0);
    }


  }

  saveItemDetails() {
    this.showItemDetails = false;
  }

  selectCard(index) {
    this.internationItemDetail = this.internationalItems[index];

    this.internationalItems.forEach(element => {
      element.selected = false;
    });
    this.internationalItems[index].selected = true;
    this.internationItemDetail = this.internationalItems[index];
  }


  createForm(controls: InternationalShipDetailForm[]) {

    let form = {};
    if (controls) {
      for (const control of controls) {
        //  form[control.fieldCode] = new FormControl(control.value);
        form[control.fieldCode] = new FormControl(control.value);
        //this.FormData.addControl(control.InternationalShipDetailForm, this.formBuilder.control(''));
      }
      this.FormData = new FormGroup(form);
    }



  }

  checkBoxChanged(event, index) {

    if (event.checked) {
      this.InternationalShipDetailFormData[index].selectedValue = "Yes";
    }
    else {
      this.InternationalShipDetailFormData[index].selectedValue = "No";
    }

  }
  SaveInternationalShipDetailFormData() {

    var obj = this.FormData.value;
    //let data = {};//
    // Object.keys(this.FormData.value).forEach(element => {
    //   const obj = 
    // });
    //this.InternationalData.InternationalFormData=Obj;

    let data = {};
    let displayData = {};
    Object.keys(obj).forEach(element => {
      if (obj[element] != null && obj[element].toString().length > 0)
        data[element] = obj[element];
      this.internationalFormData[element]=obj[element];
    });
    this.internationalEntity.lstEntityLineItemInternationaldetails =[];
    this.InternationalData.InternationalFormData= {} as InvoiceCopy;
    this.InternationalData.InternationalItem=[];
    if(this.internationalFormData)
    {
    this.InternationalData.InternationalFormData.terms=this.internationalFormData.TermsOfShipment?? '';
    this.InternationalData.InternationalFormData.reason=this.internationalFormData.ReasonForExport?? '';
    this.InternationalData.InternationalFormData.paperlessInvoice=this.internationalFormData.PaperlessInvoice ?? false;
    this.InternationalData.InternationalFormData.invno=this.internationalFormData.InvoiceNumber ?? '';
    this.InternationalData.InternationalFormData.pono=this.internationalFormData.PurchaseOrderNumber ?? '';
    this.InternationalData.InternationalFormData.descriptionOfOthers=this.internationalFormData.DescriptionOfOthers?? '';
    this.InternationalData.InternationalFormData.dscount="";
    this.InternationalData.InternationalFormData.insurence=this.internationalFormData.Insurance?? '';
    this.InternationalData.InternationalFormData.others=this.internationalFormData.Others ?? '';
    this.InternationalData.InternationalFormData.bookingConfirmationNo=this.internationalFormData.BookingConfirmationNumber?? '';
    this.InternationalData.InternationalFormData.brokerCode=this.internationalFormData.BrokerCode?? '';
    this.InternationalData.InternationalFormData.b13fillingOption=this.internationalFormData.b13fillingOption?? 0;
    this.InternationalData.InternationalFormData.freight="";
    this.InternationalData.InternationalFormData.cerorgstatus=this.internationalFormData.CertificateofOrigin !=null ? this.internationalFormData.CertificateofOrigin.toString():"False";
    this.InternationalData.InternationalFormData.shipfromtaxid=this.internationalFormData.ShipFromTaxID??''; // this.InternationalData.InternationalFormData.s=this.internationalFormData.ShipFromTaxID2;
    this.InternationalData.InternationalFormData.shiptotaxid=this.internationalFormData.ShipToTaxID??'';
    this.InternationalData.InternationalFormData.shipToTaxIdtype=this.internationalFormData.ShipToTaxID3??'';
    this.InternationalData.InternationalFormData.soldtotaxid=this.internationalFormData.SoldToTaxID??'';
    this.InternationalData.InternationalFormData.soldToInfoCode=this.internationalFormData.SoldToInformation??'';
    this.InternationalData.InternationalFormData.dutypayer=this.internationalFormData.DutiesPaymentPaye??'';
    this.InternationalData.InternationalFormData.dutyaccno=this.internationalFormData.DutiesAccountNumber??'';
    this.InternationalData.InternationalFormData.eelpfc="";
    this.InternationalData.InternationalFormData.nafta=false;
    this.InternationalData.InternationalFormData.currencyType="";
    this.InternationalData.InternationalFormData.offShoreReturn=this.internationalFormData.OffShoreReturn??'';
    this.InternationalData.InternationalFormData.blanketPeriodFrom="";
    this.InternationalData.InternationalFormData.blanketPeriodTo="";
    this.InternationalData.InternationalFormData.importerOfRecordCode=this.internationalFormData.ImporterOfRecord??'';
    this.InternationalData.InternationalFormData.handlingid="";
    this.InternationalData.InternationalFormData.freightmode=this.internationalFormData.FreightInclude!=null ? this.internationalFormData.FreightInclude.toString():"False";
    this.InternationalData.InternationalFormData.itnNumber=this.internationalFormData.ITNNumber??'';
    this.InternationalData.InternationalFormData.shippingNo="";
    }
    this.internationalItems.forEach(data=>{
      const entityLineItemInternationalDetails = {} as ExportInvoice;
      entityLineItemInternationalDetails.total = data.intTotalItemQuantity.toString()??'';
      entityLineItemInternationalDetails.eccno=   data.objInternationalDetails.strECCN?? '';
      entityLineItemInternationalDetails.declare=   data.objInternationalDetails.dblDeclaredValue.toString()??"";
      entityLineItemInternationalDetails.desc=   data.strItemDescription??"";
      entityLineItemInternationalDetails.handlingid= "";
      entityLineItemInternationalDetails.harmonizedCode=data.objInternationalDetails.strHarmonizedCode??"";
      entityLineItemInternationalDetails.licence= data.objInternationalDetails.strLicenceCode??"";
      entityLineItemInternationalDetails.orgcountry=data.objInternationalDetails.strCountryOfOrigin??"";
     entityLineItemInternationalDetails.qty= data.intTotalItemQuantity.toString()??"";
      entityLineItemInternationalDetails.preferenceCriteria=data.objInternationalDetails.preferenceCriteria??"";
      entityLineItemInternationalDetails.weight= data.dblPieceWeight.toString()??'';
      entityLineItemInternationalDetails.uom="";
      entityLineItemInternationalDetails.expdate="";
      entityLineItemInternationalDetails.itemno=  data.strItemNumber??"";
      entityLineItemInternationalDetails.shippingNo="";
      this.InternationalData.InternationalItem.push(entityLineItemInternationalDetails);
      });
    console.log(this.InternationalData);
    this.internationalLstItems.emit(this.InternationalData);
    this.closePopup.emit();
    for (let data in obj) {

      console.log(data);
    }
  }
  ClosePopup() {

    this.closePopup.emit()
  }
  Addrow() {
    let indexForId = this.internationalItems.length + 1
    let rowItem = {} as LstLineItem;
    rowItem.dblPieceWeight = 0;
    rowItem.intOrderedQuantity = 0;
    rowItem.objInternationalDetails = {} as ObjInternationalDetails;
    rowItem.strItemDescription = '';
    rowItem.strItemNumber = '';
    rowItem.IsEditable = true;
    this.internationalItems.push(rowItem);

  }
  cancelrowitem(item) {
    // with this you set all the rows as no editable, so you can change from row to row with the edit button
    // this.internationalItems.filter(row => row.IsEditable).map(r => { r.IsEditable = false; return r })
    // item.IsEditable = true;
    const itemIndex = this.internationalItems.findIndex(x => x.strItemNumber == item.strItemNumber);
    this.internationalItems.splice(itemIndex, 1);

  }
  EditAll() {
    this.IsEditAll = true;
  }
  CancelAllitem() {
    this.IsEditAll = false;
  }
  ApplyAll() {
    this.internationalItems.map(row => {
      if (row.strItemNumber.length > 0 && row.strItemDescription != '' && row.intOrderedQuantity > 0)
        row.IsEditable = false;
    });
    this.IsEditAll = false;
  }
  saveItem(item) {
    if (item.strItemNumber > 0 && item.strItemDescription != '' && item.intOrderedQuantity > 0) {
      item.IsEditable = false;
    }
  }

  GetDropDownData(data: InternationalShipDetailForm) {
    if (data.dropDownValues && data.dropDownValues.length > 0) {
      // Do Nothing
    }
    else {
      this.processShipperService.getDropDownBindData(data.columnTextField, data.columnValueField, data.tableName).subscribe(element => {
        console.log(element);
        data.dropDownValues = element;
       // this.FormData.controls[data.fieldCode].setValue()
        //this.FormData.updateValueAndValidity();
        this.cd.detectChanges();
      });
    }
  }
}


