  batchConfigurationDTOModel: batchConfigurationDTOModel = { bpfileField: [] };
  fileFormatList: string[] = [".CSV", ".XLSX\\.XLS"];
  constructor(
    private bcService: BatchConfigurationService,
    private dataService: DataService,
    private router: Router,
    private excelService: ExcelService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.items = this.items = [
      {
        label: "Add",
        icon: "pi pi-refresh",
        command: () => {
          this.addFields("");
        },
      },
      {
        label: "Remove",
        icon: "pi pi-times",
        command: () => {
          this.addFields("Remove");
        },
      },
      {
        label: "Add Default Values",
        icon: "pi pi-times",
        command: () => {
          this.addFields("Default");
        },
      },
    ];

    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;
      console.log(this.id);
      if (this.id > 0) {
        this.breadcrumb = "Update";
        this.bcService.getBatchConfigData(this.id).subscribe((data) => {
          console.log(data);
          this.batchConfigurationDTOModel = data;
          data.bpfileField.forEach((item) => {
            this.selectedList.push(item);
          });
        });
      }
    });
  }

  addFields(val) {
    if (val == "Default") {
      this.sourcelist
        .filter((x) => x.isDefault)
        .forEach((element) => {
          this.selectedList.push(JSON.parse(JSON.stringify(element)));
        });
    } else if (val == "Remove") {
      this.selectedList = this.selectedList.filter((x) => !x.selected);
    } else {
      this.sourcelist.forEach((element) => {
        if (element.selected) {
          element.selected = false;
          this.selectedList.push(JSON.parse(JSON.stringify(element)));
        }
      });
    }
  }

  save() {
    this.batchConfigurationDTOModel;

    let object = {};
    this.selectedList.forEach((element, index) => {
      object[element.fieldName] = index;
      this.batchConfigurationDTOModel.bpfileField.push({
        fieldIndex: index,
        fieldName: element.fieldName,
        fileFormatName: this.batchConfigurationDTOModel.fileFormatName,
      });
    });
    this.batchConfigurationDTOModel.bpfileTemplate = object;
    this.bcService.save(this.batchConfigurationDTOModel).subscribe((data) => {
      //this.data = data;
    });
  }

  drop(event: CdkDragDrop<string[]>) {
    console.log(event.previousIndex, event.currentIndex);
    this.selectedList[event.previousIndex].fieldIndex = event.currentIndex;
    moveItemInArray(this.selectedList, event.previousIndex, event.currentIndex);
  }

  exportTemplate() {
    let object = {};
    this.selectedList.forEach((element) => {
      object[element.fieldName] = "";
    });
    this.excelService.exportAsExcelFile([object], "testFile");
    console.log("obj", object);
  }
