import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import {
  CarrierConfigurationModel,
  CarrierIntegrationConfigurationModel,
} from "../models/carrierConfigration.model";

@Injectable({
  providedIn: "root",
})
export class CarrierConfigurationService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  get() {
    return this.http.get<CarrierConfigurationModel[]>(
      this.gs.baseURL + "api/CarrierConfigs"
    );
  }

  getCarrierIntegration() {
    return this.http.get<CarrierIntegrationConfigurationModel[]>(
      this.gs.baseURL + "api/CarrierIntegrationConfiguration"
    );
  }
}
