import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ShipViaCodeConfigurationModel } from "src/app/models/shipviaCodeConfiguration.model";
import { systemConfigurationModel } from "src/app/models/systemConfiguration.model";
import { configurationService } from "src/app/services/configuration-service";
import { DataService } from "src/app/services/data.service";
import { shipViaCodeConfigurationService } from "src/app/services/ship-via-code-configuration.service";

@Component({
  selector: "app-system-configuration",
  templateUrl: "./system-configuration.component.html",
  styleUrls: ["./system-configuration.component.scss"],
})
export class SystemConfigurationComponent implements OnInit {
  columns = ["Key", "Value", "Answer"];
  data: systemConfigurationModel[] = [];
  isEdit = false;
  systemConfigurationBackupValue = [];
  previewData = [];
  showPreviewPopup: boolean = false;
  constructor(
    private svcService: configurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.svcService.getSystemConfiguration().subscribe((data) => {
      this.systemConfigurationBackupValue = JSON.parse(JSON.stringify(data));
      this.data = data;
    });
  }
  editAll() {
    this.isEdit = true;
  }
  preview() {
    this.showPreviewPopup = true;

    this.previewData = this.data.filter(
      ({ value: id1 }) =>
        !this.systemConfigurationBackupValue.some(
          ({ value: id2 }) => id2 == id1
        )
    );

    console.log("diff", this.previewData);
  }
  save() {}
  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
}
