import {
  Component,
  ElementRef,
  OnInit,
  Renderer2,
  ViewChild,
} from "@angular/core";
import { ProcessShipperService } from "src/app/services/process-shipper.service";
import { BehaviorSubject, forkJoin } from "rxjs";
import { DataService } from "src/app/services/data.service";
import _ from "lodash";
import {
  AssociatedCarriers,
  CarrierDataModel,
  CarrierService,
  EntityAddress,
  Lookup,
  SelectedData,
  ShipmentDataModel,
  UserDefaultDataModel,
  SenderConfiguration,
  PlantDataModel,
  XcarrierCode,
  PackageType,
  Dimension,
  EntityContainer,
  InterfaceLineItemDetails,
  LstLineItem,
} from "./process-shipping.model";
import { lookup } from "dns";
import {
  BoxTypeDTO,
  CarrierServiceDTO,
  CarriersDTO,
  UserDefaultDataDTO,
} from "../models/default-data";
import {
  JsonFormData,
  SpecialServiceDetialDTO,
  SpeicalServiceSelectedData,
} from "../../models/special-services.model";
import { SpecialServicesService } from "src/app/services/specialService.service";
import { ActivatedRoute } from "@angular/router";

import { CarrierConfigs } from "../configuration/user-registration/user-registration-model";
import { printLabel, GetCommunicatorDetails } from "../../javascriptfun.js";
import { ExportInvoice, InternationalEntity, InternationalShipDetailFormData, InvoiceCopy } from "./international-form/international-form.model";
@Component({
  selector: "app-process-shipping",
  templateUrl: "./process-shipping.component.html",
  styleUrls: ["./process-shipping.component.scss"],
})
export class ProcessShippingComponent implements OnInit {
  message: string;
  shipmentSuccessful: boolean;
  filterSectionExpanded = false;
  userDefaultData: UserDefaultDataModel;
  carrierConfig: AssociatedCarriers[] = [];
  carrierList: Lookup[] = [];
  carrierService: CarrierService[] = [];
  carrierServiceList: Lookup[] = [];
  blnSaveAddress: boolean = false;
  stateOptions: any[] = [
    { label: "Ship To", value: "shipto" },
    { label: "Ship From", value: "shipfrom" },
  ];
  shippingDetailsTab: string = "shipto";
  paymentList: Lookup[] = [];
  paymentData: string[] = [];
  selectedData: SelectedData = {
    shipDate: new Date(),
    deliveryDate: new Date(),
  } as SelectedData;
  shipFrom: EntityAddress = {} as EntityAddress;
  shipTo: EntityAddress = {} as EntityAddress;
  senderconfig: SenderConfiguration;
  visibleSpecialService: boolean = false;
  pullPlannedshipdata: ShipmentDataModel;
  callpullPlannedshipdataAPI: boolean = false;
  service: Lookup[] = [];
  accountList: Lookup[] = [];
  boxTypeList: Lookup[] = [];
  dimentionList: Lookup[] = [];
  containerData: EntityContainer[];
  itemData: InterfaceLineItemDetails[];
  CarrierData: CarrierDataModel;
  itemSectionExpanded = false;
  specialServiceSectionExpanded = false;
  shipPostData: ShipmentDataModel;
  plantData: PlantDataModel;
  SearchValue: string = "100";
  shipViaCodeList: Lookup[] = [];
  showInternationalForm = false;
  SelectedShipViaCode: XcarrierCode;
  plantId = "Plant001";
  public formData: SpecialServiceDetialDTO[];
  packageType: PackageType[];
  packageTypeId: number;
  selectedErpId: number;
  packingOn: boolean = false;
  Isprepacked: boolean = false;
  showMoreSpecialService: boolean = false;
  showCancel: boolean = false;
  specialServiceFormData: SpeicalServiceSelectedData;
  internationalItems: LstLineItem[] = [];
  openNewDocPopup: boolean;
  autoReviewShip: boolean = false;
  Packagedata: EntityContainer = {} as EntityContainer;
  deliveryDocList: any;
  pdfSrc: any;
  internationalshipFromdata?:InvoiceCopy={} as InvoiceCopy;
  objInternationalDetails?: ExportInvoice[] = [];
  internationalEntity:InternationalEntity={} as InternationalEntity;
  @ViewChild("MacAddress") el: ElementRef;
  constructor(
    private processShipperService: ProcessShipperService,
    private activatedRoute: ActivatedRoute,
    private dataService: DataService,
    private specialService: SpecialServicesService,
    private rd: Renderer2
  ) { }

  ngOnInit(): void {
    this.paymentList = [
      { textField: "Sender", valueField: "Sender" },
      { textField: "Recipient", valueField: "Recipient" },
      { textField: "Third Party", valueField: "ThirdParty" },
    ];
    this.shipFrom = {} as EntityAddress;
    this.shipTo = {} as EntityAddress;
    this.SearchValue = "";
    this.callpullPlannedshipdataAPI = false;

    this.dataService.plantChangeObs.subscribe((selectedPlant) => {
      //console.log('plantchange', selectedPlant);
      this.selectedData = {
        shipDate: new Date(),
        deliveryDate: new Date(),
      } as SelectedData;
      this.selectedData.PlantId = selectedPlant;
      this.dataService.selectedPlantId = selectedPlant;

      if (
        this.userDefaultData &&
        selectedPlant == this.userDefaultData.defaultPlantId
      ) {
        this.setSelectedData();
      }
      //this.selectedData.CarrierId = null;
      this.initData();
      this.shipTo = {} as EntityAddress;
    });
    this.dataService.defaultErpObs.subscribe((selectederp) => {
      this.selectedErpId = selectederp;
      this.dataService
        .getERPData(this.selectedData.PlantId)
        .subscribe((erpres) => {
          this.selectedData.ERP = erpres["erp"].find(
            (x) => x.toolKitId == this.selectedErpId
          ).erpName;
          if (this.selectedErpId > 0) {
            this.Isprepacked = erpres["erp"].find(
              (x) => x.toolKitId == this.selectedErpId
            ).isPrepacked;
            this.packingOn = erpres["erp"].find(
              (x) => x.toolKitId == this.selectedErpId
            ).packType;
          } else {
            this.Isprepacked = false;
            this.packingOn = false;
          }
        });
    });

    this.getDefaultData();
    this.dataService.searchClickObs.subscribe((searchValue) => {
      if (searchValue != null) {
        this.SearchValue = searchValue;
        setTimeout(() => {
          this.pullPlannedShipData();
        }, 1000);

        this.itemSectionExpanded = true;
      }
    });

    this.activatedRoute.params.subscribe((paramsId) => {
      this.SearchValue = paramsId.id;
      if (this.SearchValue) {
        this.dataService.searchCLick.next(this.SearchValue); // this.pullPlannedShipData();
        this.callpullPlannedshipdataAPI = true;
        this.itemSectionExpanded = true;
      }
    });

    this.dataService.openDeliveryDoc.subscribe((searchValue) => {
      if (searchValue != null) {
        this.openNewDocPopup = searchValue;
        const date = new Date();
        this.processShipperService
          .getDeliveryDoc(this.dataService.dateToISOLikeButLocal(date))
          .subscribe((data) => {
            this.deliveryDocList = data;
          });
      }
    });
  }
  setShipTab() {
    if (
      this.selectedData.ERP &&
      this.selectedData.ERP.toLowerCase() == "manual"
    ) {
      this.shippingDetailsTab = "shipfrom";
    } else {
      this.shippingDetailsTab = "shipto";
    }
  }
  getDefaultData() {
    this.dataService.getDefaultData().subscribe((res) => {
      console.log("defData", res);
      this.userDefaultData = res;
      this.selectedData.PlantId = this.userDefaultData.defaultPlantId;
      this.selectedData.ERP = this.userDefaultData.userDefaultERP;
      this.selectedData.CarrierCode = this.userDefaultData.defaultCarrier;
      this.selectedData.toolKitId = this.userDefaultData.toolKitId;
      this.selectedData.CarrierId = this.userDefaultData.defaultCarrierConfigId;
      this.selectedData.CarrierServiceId =
        this.userDefaultData.defaultServiceId;
      this.initData();
    });
  }

  initData() {
    this.plantData = {} as PlantDataModel;
    const apis: any[] = [];
    apis.push(
      this.processShipperService.getPlantDetail(this.selectedData.PlantId)
    );
    apis.push(
      this.processShipperService.getCarriers(this.selectedData.PlantId)
    );
    apis.push(this.processShipperService.getIntegrationId());
    apis.push(
      this.processShipperService.getBoxTypeData(this.selectedData.PlantId)
    );
    apis.push(this.processShipperService.getDimensions());
    apis.push(
      this.processShipperService.getShipViaCode(this.selectedData.PlantId)
    );

    forkJoin(apis).subscribe((res) => {
      this.plantData.plantDetail = res[0] as SenderConfiguration;
      if (this.plantData.plantDetail) {
        this.getplantdetailsdata();
      }
      this.plantData.associatedCarriers = res[1] as CarriersDTO[];
      //this.plantData.integIdUseAsCarrierName= res[2] as number[];
      if (this.plantData.associatedCarriers) {
        this.setCarrierData();
      }
      this.plantData.boxType = res[3] as BoxTypeDTO[];
      if (this.plantData.boxType) {
        this.boxTypeList = this.plantData.boxType.map((x) => {
          return { textField: x.description, valueField: x.id } as Lookup;
        });
        // this.boxTypeList.unshift({
        //   textField: "Add New Box Type",
        //   valueField: 0,
        // });
      }
      this.plantData.dimension = res[4] as Dimension[];
      if (this.plantData.dimension) {
        this.dimentionList = this.plantData.dimension.map((x) => {
          return {
            textField: (x.width + "X" + x.height + "X" + x.length)?.toString(),
            valueField: x.slno?.toString(),
          } as Lookup;
        });
      }
      this.plantData.shipViaCode = res[5] as XcarrierCode[];
      if (this.plantData.shipViaCode) {
        this.shipViaCodeList = this.plantData.shipViaCode.map((x) => {
          return {
            textField: (
              x.carriercode +
              " - " +
              x.carrier +
              " - " +
              x.txacc +
              " - " +
              x.service
            )?.toString(),
            valueField: x.carriercode,
          } as Lookup;
        });
      }
    });
  }
  getplantdetailsdata() {
    this.shipFrom.strAddressLine1 = this.plantData.plantDetail.line1;
    this.shipFrom.strAddressLine2 = this.plantData.plantDetail.line2;
    this.shipFrom.strAddressLine3 = this.plantData.plantDetail.addressLine3;
    this.shipFrom.strCity = this.plantData.plantDetail.city;
    this.shipFrom.strCompanyName = this.plantData.plantDetail.compName;
    this.shipFrom.strContactName = this.plantData.plantDetail.contactName;
    this.shipFrom.strCountryCode = this.plantData.plantDetail.countryCode;
    this.shipFrom.strCountryName = this.plantData.plantDetail.countryName;
    this.shipFrom.strEmailAddress = this.plantData.plantDetail.email;
    this.shipFrom.strFaxNumber = this.plantData.plantDetail.fax;
    this.shipFrom.strPhoneNumber = this.plantData.plantDetail.phone;
    this.shipFrom.strPostalCode = this.plantData.plantDetail.postalCode;
    this.shipFrom.strState = this.plantData.plantDetail.stateCode;
  }

  setCarrierData() {
    this.carrierConfig = this.plantData.associatedCarriers;
    if (this.selectedData.CarrierId) {
      this.selectedData.IntegrationId = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      )?.integrationId;
      const selectedCarrierCode = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      ).carrierCode;
      this.setCarrierCode();

      if (selectedCarrierCode && this.selectedData.IntegrationId > 0) {
        const apis: any[] = [];
        apis.push(
          this.processShipperService.getCarrierService(
            selectedCarrierCode,
            this.selectedData.IntegrationId
          )
        );
        apis.push(
          this.processShipperService.getPaymentTypes(selectedCarrierCode)
        );
        apis.push(
          this.processShipperService.getPackageType(selectedCarrierCode)
        );
        forkJoin(apis).subscribe((res) => {
          this.carrierService = res[0] as CarrierService[];
          if (this.carrierService) {
            this.carrierService = this.carrierService.filter(
              (x) =>
                x.countryMappings.toLowerCase().indexOf("all") >= 0 ||
                x.countryMappings.indexOf(
                  this.plantData.plantDetail.countryCode
                ) >= 0
            );
            this.carrierServiceList = this.carrierService.map((x) => {
              return { textField: x.serviceName, valueField: x.id } as Lookup;
            });
            this.selectedData.ServiceName = this.carrierServiceList.find(
              (x) => x.valueField == this.selectedData.CarrierServiceId
            )?.textField;
          }
          this.paymentData = res[1] as string[];
          if (this.paymentData) {
            this.paymentList = this.paymentData.map((x) => {
              return { textField: x, valueField: x } as Lookup;
            });
          }
          this.packageType = res[2] as PackageType[];
          if (this.packageType) {
            this.packageTypeId = this.packageType.find(
              (x) => x.isDefault == true
            ).id;
          }
        });
      }
    } else {
      this.carrierServiceList = [];
      this.paymentList = [];
    }
    this.setCarriers();
  }

  setSelectedData() {
    if (
      this.userDefaultData &&
      this.userDefaultData.defaultPlantId == this.selectedData.PlantId
    ) {
      //Default Plant - Page Load
      this.selectedData.ERP = this.userDefaultData.userDefaultERP;
      this.selectedData.CarrierCode = this.userDefaultData.defaultCarrier;
      this.selectedData.toolKitId = this.userDefaultData.toolKitId;
      this.selectedData.CarrierServiceId =
        this.userDefaultData.defaultServiceId;
      this.selectedData.CarrierId = this.userDefaultData.defaultCarrierConfigId;
    }

    this.boxTypeList = this.plantData.boxType.map((x) => {
      return { textField: x.description, valueField: x.description } as Lookup;
    });

    this.dimentionList = this.plantData.dimension.map((x) => {
      return {
        textField: (x.width + "X" + x.height + "X" + x.length)?.toString(),
        valueField: x.slno?.toString(),
      } as Lookup;
    });
  }
  setCarriers() {
    let integIdUseAsCarrierName = this.plantData.integIdUseAsCarrierName;
    this.carrierList = this.carrierConfig.map((x) => {
      return {
        textField:
          x.useAsCarrierName == true
            ? x.carrierCode +
            (x.accountNumber && x.accountNumber.length > 0
              ? " - " + x.accountNumber
              : x.carrierName && x.carrierName.length > 0
                ? " - " + x.carrierName
                : "")
            : x.carrierName +
            (x.accountNumber && x.accountNumber.length > 0
              ? "-" + x.accountNumber
              : ""),
        valueField: x.id,
      } as Lookup;
    });
    if (this.selectedData.CarrierId > 0) {
      //this.selectedData.CarrierCode = this.carrierConfig.find(x => x.id = this.selectedData.CarrierId).carrierCode;
      this.accountList = this.carrierConfig
        .filter((x) => x.id == this.selectedData.CarrierId)
        .map((x) => {
          return {
            textField: x.accountNumber + " - " + x.carrierName,
            valueField: x.accountNumber.toString(),
          } as Lookup;
        });
      this.selectedData.AccountNumber = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      ).accountNumber;
    }
  }
  OnSelectCarrier(value) {
    if (this.selectedData.CarrierId > 0) {
      this.selectedData.IntegrationId = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      ).integrationId;

      this.accountList = this.carrierConfig
        .filter((x) => x.id == this.selectedData.CarrierId)
        .map((x) => {
          return {
            textField: x.accountNumber + " - " + x.carrierName,
            valueField: x.accountNumber.toString(),
          } as Lookup;
        });
      this.selectedData.AccountNumber = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      ).accountNumber;

      const selectedCarrierCode = this.carrierConfig.find(
        (x) => x.id == this.selectedData.CarrierId
      ).carrierCode;

      if (selectedCarrierCode && this.selectedData.IntegrationId > 0) {
        const apis: any[] = [];
        apis.push(
          this.processShipperService.getCarrierService(
            selectedCarrierCode,
            this.selectedData.IntegrationId
          )
        );
        apis.push(
          this.processShipperService.getPaymentTypes(selectedCarrierCode)
        );
        apis.push(
          this.processShipperService.getPackageType(selectedCarrierCode)
        );
        this.itemSectionExpanded = false;
        forkJoin(apis).subscribe((res) => {
          this.carrierService = res[0] as CarrierService[];
          if (this.carrierService) {
            this.carrierService = this.carrierService.filter(
              (x) =>
                x.countryMappings.toLowerCase().indexOf("all") >= 0 ||
                x.countryMappings.indexOf(
                  this.plantData.plantDetail.countryCode
                ) >= 0
            );
            this.carrierServiceList = this.carrierService.map((x) => {
              return { textField: x.serviceName, valueField: x.id } as Lookup;
            });
            this.selectedData.ServiceName = this.carrierServiceList.find(
              (x) => x.valueField == this.selectedData.CarrierServiceId
            )?.textField;
          }
          this.paymentData = res[1] as string[];
          if (this.paymentData) {
            this.paymentList = this.paymentData.map((x) => {
              return { textField: x, valueField: x } as Lookup;
            });
          }
          this.packageType = res[2] as PackageType[];
          if (this.packageType) {
            this.packageTypeId = this.packageType.find(
              (x) => x.isDefault == true
            ).id;
          }
          this.itemSectionExpanded = true;
          console.log("hasrhit11", this.CarrierData);
        });
      }
    } else {
      this.carrierServiceList = [];
      this.paymentList = [
        { textField: "Sender", valueField: "Sender" },
        { textField: "Recipient", valueField: "Recipient" },
        { textField: "Third Party", valueField: "ThirdParty" },
      ];
    }
  }

  pullPlannedShipData() {
    const data: any = {
      PlantId: this.selectedData.PlantId,
      ERPName: this.selectedData.ERP,
      PullType: "Online",
      PullBy: "Deldoc",
      Identifier1: this.SearchValue,
      Identifier2: "",
    };
    this.showCancel = false;
    this.processShipperService.pullPlannedShipData(data).subscribe((x) => {
      this.fillData(x);
      if (this.packageType) {
        this.packageTypeId = this.packageType.find(
          (x) => x.isDefault == true
        ).id;
      }

      if (this.selectedData.ShipViaCode) {
        this.OnSelectShipViaCode();
      }

      this.shipFrom = x.objShipFrom;
      this.shipTo = x.objShipTo;
      this.autoReviewShip = false;
    });
  }
  clear() {
    this.ngOnInit();
    this.containerData=[];
    this.showCancel = false;
  }
  ship() {
    this.shipPostData = {
      bodMsgId: 1,
      strPlantID: this.selectedData.PlantId,
      erp: this.selectedData.ERP,
      senderId: 1,
      pullBy: "DELDOC",
      identifier1: this.SearchValue,
    };
    GetCommunicatorDetails();
    this.shipPostData.macAddress = this.el.nativeElement.value;
    if (this.pullPlannedshipdata.bodMsgId > 0) {
      this.shipPostData.bodMsgId = this.pullPlannedshipdata.bodMsgId;
    }
    this.shipPostData.carrierServiceId = this.selectedData.CarrierServiceId;
    this.shipPostData.carrierConfigId = this.selectedData.CarrierId;
    this.shipPostData.serviceCode = this.carrierService.find(
      (x) => x.id == this.selectedData.CarrierServiceId
    )?.serviceCode;
    this.shipPostData.strPlantID = this.selectedData.PlantId;
    this.shipPostData.payment = this.selectedData.Payment;
    this.shipPostData.serviceName = this.selectedData.ServiceName;
    this.shipPostData.accountNumber = this.selectedData.AccountNumber;
    this.shipPostData.lstContainer = this.containerData;
    this.shipPostData.objShipFrom = this.shipFrom;
    this.shipPostData.objShipTo = this.shipTo;
    // this.shipPostData.lstContainer.forEach(data=>
    //   data.lstPackageGroup.forEach(x=>
    //     x.lstLineItem.forEach(int=>
    //       this.internationalItems.forEach(x=>
    //         int.objInternationalDetails=x.objInternationalDetails) )

    //   ));
    this.shipPostData.objInternationalDetails=this.objInternationalDetails;
    this.shipPostData.internationalshipformdata = this.internationalshipFromdata;
    //this.shipPostData.internationalEntity =this.internationalEntity;
    if (
      this.specialServiceFormData &&
      this.specialServiceFormData.speicalServicePostData
    ) {
      this.shipPostData.specialServices =
        this.specialServiceFormData.speicalServicePostData;
    }
    //this.itemSectionExpanded=false;
    this.processShipperService.shipData(this.shipPostData).subscribe((x) => {
      if (x.shipmentSuccessful) {
        this.showCancel = true;
        this.shipmentSuccessful = x.shipmentSuccessful;
        this.message = x.message;
               this.objInternationalDetails= x.exportInvoices;

        this.containerData = JSON.parse(JSON.stringify(x.container));
        x.container.forEach((element) => {
          if (x.popUp) {
            localStorage.setItem(
              element.intHandlingUnitID,
              element.lstReport[0].label
            );
            setTimeout((s) => {
              window.open(
                `${window.location.origin}/pop-up?HandlingUnitID=` +
                element.intHandlingUnitID.toString(),
                "_blank",
                "width=480, height=660"
              );
            }, 1000);
          }
          else {
            var strScript =
              "SPrint.pagecreate<~~!~~paramseperator~~!~~>" +
              element.lstReport[0].base64 +
              "<~~!~~paramseperator~~!~~>"+ x.printerName  +"<~~!~~paramseperator~~!~~>"+ x.numberOfCopies;
            printLabel(strScript);
          }

        });
        this.ReportClick();
        this.processShipperService
          .getCustomizeDelDoc(this.selectedData.PlantId)
          .subscribe((data) => {
            this.autoReviewShip = data.autoReviewShip;
            if (!this.autoReviewShip) {
              this.containerData = [];
              this.initData();
            }
          });
        this.Packagedata.dblTotalWeight = x.totalWeight;
        this.Packagedata.dblTotalContainerCost = x.shipmentCost;
        //this.ship

        this.itemSectionExpanded = true;
      }

      this.message = x.message;
      this.dataService.footerData.next({
        shipmentCost: x.shipmentCost,
        totalWeight: x.totalWeight,
      });
    });
  }

  showSpecialService() {
    this.specialService
      .getData("2", "UPS")
      .then((data) => {
        this.formData = data;
        this.formData.forEach((element) => {
          if (
            this.specialServiceFormData &&
            this.specialServiceFormData.speicalServicePostData
          ) {
            if (
              this.specialServiceFormData.speicalServicePostData[
              element.specialServiceName
              ] &&
              this.specialServiceFormData.speicalServicePostData[
                element.specialServiceName
              ].toString().length > 0
            ) {
              element.value =
                this.specialServiceFormData.speicalServicePostData[
                element.specialServiceName
                ];
              if (element.controlTypeCode.toLowerCase() == "chk") {
                if (element.value.toString() == "true")
                  element.selectedValue = "Yes";
                else element.selectedValue = "No";
              }
            }
          } else {
            this.formData.forEach((w) => (w.selectedValue = "No"));
          }
        });
      })
      .catch((error) => {
        console.log(error);
      });

    this.visibleSpecialService = true;
  }
  closeInternational() {
    this.showInternationalForm = false;
  }
  closeSpecialService(data: any) {
    this.specialServiceFormData = data;

    Object.keys(this.specialServiceFormData.speicalServicePostData).forEach(
      (element, index) => {
        if (index > 3) {
          this.showMoreSpecialService = true;
        } else {
          this.showMoreSpecialService = false;
        }
      }
    );
    this.visibleSpecialService = false;
  }
  RemoveSpeicalService(data: string) {
    delete this.specialServiceFormData.speicalServicePostData[data];
    delete this.specialServiceFormData.specialServiceDisplay[data];
  }
  OnSelectShipViaCode() {
    if (this.selectedData.ShipViaCode) {
      const selectedShipViaCode = this.plantData.shipViaCode.find(
        (x) => x.carriercode.toString() == this.selectedData.ShipViaCode
      );
      if (selectedShipViaCode) {
        this.SelectedShipViaCode = selectedShipViaCode;
        this.selectedData.AccountNumber = selectedShipViaCode.txacc;
        let tempCarrier = this.carrierConfig.filter(
          (x) =>
            x.carrierCode == selectedShipViaCode.carrCode &&
            x.accountNumber == selectedShipViaCode.txacc &&
            (!this.selectedData.AccountNumber ||
              x.accountNumber == this.selectedData.AccountNumber)
        );
        if (tempCarrier && tempCarrier.length > 0) {
          this.selectedData.CarrierId = tempCarrier[0].id;
          this.selectedData.IntegrationId = tempCarrier[0].integrationId;
          //this.selectedData.CarrierCode = tempCarrier[0].carrierCode;
          this.setCarrierCode();
          if (this.SelectedShipViaCode) {
            this.processShipperService
              .getCarrierService(
                tempCarrier[0].carrierCode,
                this.selectedData.IntegrationId
              )
              .subscribe((data) => {
                this.carrierService = data;
                this.carrierService = this.carrierService.filter(
                  (x) =>
                    x.countryMappings.toLowerCase().indexOf("all") >= 0 ||
                    x.countryMappings.indexOf(
                      this.plantData.plantDetail.countryCode
                    ) >= 0
                );
                this.carrierServiceList = this.carrierService.map((x) => {
                  return {
                    textField: x.serviceName,
                    valueField: x.id,
                  } as Lookup;
                });
              });
            setTimeout(() => {
              if (this.carrierService) {
                this.selectedData.CarrierServiceId =
                  this.carrierServiceList.find(
                    (x) => x.textField == this.SelectedShipViaCode.service
                  )?.valueField;
                this.selectedData.ServiceName = this.carrierServiceList.find(
                  (x) => x.textField == this.SelectedShipViaCode.service
                )?.textField;
              }
            }, 50);
          }
        }
        this.selectedData.Payment = selectedShipViaCode.billto;
      }
    }
  }
  getInternationalItemsData(data) {
    this.internationalItems = data;
  }

  getPackageDetails(data) {
    this.containerData = data;
  }
  getInternationalDataItems(data:InternationalShipDetailFormData) {
    this.objInternationalDetails = data.InternationalItem;
    this.internationalshipFromdata = data.InternationalFormData;
   // this.internationalEntity=data;
  }

  openDeliveryDocDetail(shippingNo) {
    this.processShipperService
      .getReviewShipmentData(shippingNo)
      .subscribe((data) => {
        this.fillData(data);
        this.itemSectionExpanded = true;
        console.log(data);
        this.openNewDocPopup = false;
        this.shipmentSuccessful = true;
        this.autoReviewShip = true;
      });
  }

  fillData(x) {
    console.log("x", x);
    this.callpullPlannedshipdataAPI = false;
    this.containerData = x.lstContainer;
    if (this.containerData) {
      this.containerData.forEach((element) => {
        element.isprepacked = true;
      });
      if (x.lstContainer) {
        this.dataService.footerData.next({
          shipmentCost: x.lstContainer
            .map((m) => m.objRates?.dblBaseBillablePrice)
            .reduce((accumulator, current) => accumulator + current, 0)
            .toFixed(2),
          totalWeight: x.lstContainer
            .map((s) => s.dblTotalWeight)
            .reduce((accumulator, current) => accumulator + current, 0)
            .toFixed(2),
        });
      }
    }
    this.selectedData.shipDate = new Date(x.dtShipDate);
    this.selectedData.deliveryDate = x.deliveryDate
      ? new Date(x.deliveryDate)
      : new Date();
    this.selectedData.poNumber = x.strPONumber;
    this.selectedData.invNumber = x.strInvoiceNumber;
    this.itemData = x.items;
    if (x.carrierData) {
      this.packageType = x.carrierData.packageType;
      this.CarrierData = x.carrierData;
    }
    this.pullPlannedshipdata = x;
    if (this.pullPlannedshipdata.shipViaCode) {
      this.selectedData.ShipViaCode = this.pullPlannedshipdata.shipViaCode;
    }
    if (this.packageType) {
      this.packageTypeId = this.packageType.find(
        (x) => x.isDefault == true
      )?.id;
    }

    if (this.selectedData.ShipViaCode) {
      this.OnSelectShipViaCode();
    }

    this.shipFrom = x.objShipFrom;
    this.shipTo = x.objShipTo;
    this.autoReviewShip = false;
  }
  ReportClick() {
    this.processShipperService.getReport().subscribe((base) => {
      this.downloadFile(base, "sample");
      console.log(base);
    });
  }

  downloadFile(base64: any, fileName: any) {
    const src = `data:application/pdf;base64,${base64}`;

    this.pdfSrc = src;
    const link = document.createElement("a");
    link.href = src;
    link.download = fileName;
    link.click();

    link.remove();
  }
  setCarrierCode() {
    const selectedCarrier = this.carrierConfig.find(
      (x) => x.id == this.selectedData.CarrierId
    );
    console.log(selectedCarrier);
    this.selectedData.CarrierCode =
      selectedCarrier.useAsCarrierName == true
        ? selectedCarrier.carrierCode +
        (selectedCarrier.accountNumber &&
          selectedCarrier.accountNumber.length > 0
          ? " - " + selectedCarrier.accountNumber
          : selectedCarrier.carrierName &&
            selectedCarrier.carrierName.length > 0
            ? " - " + selectedCarrier.carrierName
            : "")
        : selectedCarrier.carrierName +
        (selectedCarrier.accountNumber &&
          selectedCarrier.accountNumber.length > 0
          ? "-" + selectedCarrier.accountNumber
          : "");
  }
}
