import { Component, NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ProcessShippingComponent } from "./functional-components/process-shipping/process-shipping.component";
import { PlantConfigurationComponent } from "./functional-components/configuration/plant-configuration/plant-configuration/plant-configuration.component";
import { EflowConfigurationComponent } from "./functional-components/configuration/eflow-configuration/eflow-configuration/eflow-configuration.component";
import { SpecialServicesComponent } from "./functional-components/special-services/special-services.component";
import { CreateEflowConfigurationComponent } from "./functional-components/configuration/eflow-configuration/create-eflow-configuration/create-eflow-configuration.component";
import { CreateErpConfigrationComponent } from "./functional-components/configuration/toolKit-configration/create-erp-configration/create-erp-configration.component";
import { ToolKitConfigrationComponent } from "./functional-components/configuration/toolKit-configration/tool-kit-configration.component";
import { UserRegistrationComponent } from "./functional-components/configuration/user-registration/user-registration/user-registration.component";
import { CreateUserRegistrationComponent } from "./functional-components/configuration/user-registration/create-user-registration/create-user-registration.component";
import { CreatePlantConfigurationComponent } from "./functional-components/configuration/plant-configuration/create-plant-configuration/create-plant-configuration.component";
import { PlantConfigurationListComponent } from "./functional-components/configuration/plant-configuration/plant-configuration-list/plant-configuration-list.component";
import { UserRegistrationListComponent } from "./functional-components/configuration/user-registration/user-registration-list/user-registration-list.component";
import { UserPermissionsComponent } from "./functional-components/configuration/user-registration/user-permissions/user-permissions.component";
import { PopUpComponent } from "./global-components/pop-up/pop-up.component";
import { BatchConfigurationComponent } from "./functional-components/configuration/batch-configuration/batch-configuration.component";
import { CreateBatchConfigurationComponent } from "./functional-components/configuration/create-batch-configuration/create-batch-configuration.component";
import { ShipViaCodeConfigurationComponent } from "./functional-components/configuration/ship-via-code-configuration/ship-via-code-configuration/ship-via-code-configuration.component";
import { CreateShipViaCodeConfigurationComponent } from "./functional-components/configuration/ship-via-code-configuration/create-ship-via-code-configuration/create-ship-via-code-configuration.component";
import { BoxTypeConfigurationsComponent } from "./functional-components/configuration/boxType-Configuration/box-type-configurations/box-type-configurations.component";
import { CreateBoxTypeConfigurationComponent } from "./functional-components/configuration/boxType-Configuration/create-boxType-Configuration/create-box-type-configuration/create-box-type-configuration.component";
import { LoginComponent } from "./functional-components/login/login.component";
import { CarrierConfigurationComponent } from "./functional-components/configuration/carrier-configuration/carrier-configuration.component";
import { CarrierIntegrationConfigurationComponent } from "./functional-components/configuration/carrier-integration-configuration/carrier-integration-configuration.component";
import { PrinterSetupComponent } from "./functional-components/configuration/printer-setup/printer-setup.component";
import { ScaleSetupComponent } from "./functional-components/configuration/scale-setup/scale-setup.component";
import { CarrierIntegrationDetailComponent } from "./functional-components/configuration/carrier-integration-configuration/carrier-integration-detail/carrier-integration-detail.component";
import { BatchProcessComponent } from "./functional-components/batch-process/batch-process.component";
import { AddressMastersComponent } from "./functional-components/configuration/address-master-configuration/address-masters/address-masters.component";
import { AddressMasterCreateComponent } from "./functional-components/configuration/address-master-configuration/address-master-create/address-master-create.component";
import { CreateHandlingChargeComponent } from "./functional-components/configuration/handling-charges-configuration/create-handling-charge/create-handling-charge.component";
import { HandlingChargesComponent } from "./functional-components/configuration/handling-charges-configuration/handling-charges/handling-charges.component";
import { SystemConfigurationComponent } from "./functional-components/configuration/system-configuration/system-configuration.component";
import { ShellComponent } from "./shell/shell.component";
import { PreviewComponent } from "./functional-components/configuration/system-configuration/preview/preview.component";
import { FastVoidComponent } from "./functional-components/configuration/fast-void/fast-void.component";
import { LogViewerComponent } from "./functional-components/configuration/log-viewer/log-viewer.component";
import { PasswordPolicyComponent } from "./functional-components/configuration/password-policy/password-policy.component";
import { DimentionsComponent } from "./functional-components/configuration/dimentions/dimentions.component";
import { AccessorialRateKeyComponent } from "./functional-components/configuration/accessorial-rate-key/accessorial-rate-key.component";
import { EflowLicenceViewerComponent } from "./functional-components/configuration/eflow-licence-viewer/eflow-licence-viewer.component";
import { CustomizationComponent } from "./functional-components/configuration/customization/customization.component";
import { LtlConfigurationComponent } from "./functional-components/configuration/ltl-configuration/ltl-configuration.component";
import { PackingListComponent } from "./functional-components/configuration/packing-list/packing-list.component";
import { BolConfigurationComponent } from "./functional-components/configuration/bol-configuration/bol-configuration.component";
import { CreatePackagingListComponent } from "./functional-components/configuration/packing-list/create/create.component";

const routes: Routes = [
  //{ path: 'err-page', component: ErrPageComponent },
  // { path: 'auth', loadChildren: () => import('./modules/generic-modules/authentication/authentication.module').then(m => m.AuthenticationModule) },
  {
    path: "",
    component: ShellComponent,
    children: [
      { path: "process-shipping", component: ProcessShippingComponent },
      {
        path: "plants",
        component: PlantConfigurationComponent,
        children: [
          { path: "", component: PlantConfigurationListComponent },
          { path: "create", component: CreatePlantConfigurationComponent },
          { path: "update/:id", component: CreatePlantConfigurationComponent },
        ],
      },
      { path: "process-shipping/:id", component: ProcessShippingComponent },
      { path: "eflow-configuration", component: EflowConfigurationComponent },
      { path: "toolkit-configration", component: ToolKitConfigrationComponent },
      {
        path: "create-eflow-configuration",
        component: CreateEflowConfigurationComponent,
      },
      {
        path: "create-eflow-configuration/:id",
        component: CreateEflowConfigurationComponent,
      },
      {
        path: "create-erp-configration",
        component: CreateErpConfigrationComponent,
      },
      {
        path: "create-erp-configration/:id",
        component: CreateErpConfigrationComponent,
      },

      { path: "process-shipping/:id", component: ProcessShippingComponent },
      { path: "eflow-configuration", component: EflowConfigurationComponent },
      { path: "toolkit-configration", component: ToolKitConfigrationComponent },
      {
        path: "ship-via-code-configuration",
        component: ShipViaCodeConfigurationComponent,
      },
      {
        path: "create-ship-via-code-configuration",
        component: CreateShipViaCodeConfigurationComponent,
      },
      {
        path: "create-ship-via-code-configuration/:id",
        component: CreateShipViaCodeConfigurationComponent,
      },
      {
        path: "create-eflow-configuration",
        component: CreateEflowConfigurationComponent,
      },
      {
        path: "create-eflow-configuration",
        component: CreateEflowConfigurationComponent,
      },
      {
        path: "create-erp-configration",
        component: CreateErpConfigrationComponent,
      },
      {
        path: "create-erp-configration/:id",
        component: CreateErpConfigrationComponent,
      },
      {
        path: "users",
        component: UserRegistrationComponent,
        children: [
          { path: "", component: UserRegistrationListComponent },
          { path: "create", component: CreateUserRegistrationComponent },
          { path: "update/:id", component: CreateUserRegistrationComponent },
          { path: "userPermission", component: UserPermissionsComponent },
          { path: "userPermission/:id", component: UserPermissionsComponent },
        ],
      },
      // Sales Order
      { path: "sp", component: SpecialServicesComponent },
      { path: "batch-configuration", component: BatchConfigurationComponent },
      {
        path: "create-batch-configuration/:id",
        component: CreateBatchConfigurationComponent,
      },
      {
        path: "create-batch-configuration",
        component: CreateBatchConfigurationComponent,
      },
      {
        path: "boxTypeConfigurations",
        component: BoxTypeConfigurationsComponent,
      },
      {
        path: "create-boxType-configuration",
        component: CreateBoxTypeConfigurationComponent,
      },
      {
        path: "create-boxType-configuration/:id",
        component: CreateBoxTypeConfigurationComponent,
      },
      {
        path: "carrierConfigurations",
        component: CarrierConfigurationComponent,
      },
      {
        path: "carrierIntegrationConfigurations",
        component: CarrierIntegrationConfigurationComponent,
      },
      {
        path: "carrierIntegrationDetail",
        component: CarrierIntegrationDetailComponent,
      },

      { path: "printer-setup", component: PrinterSetupComponent },
      { path: "scale-setup", component: ScaleSetupComponent },
      { path: "batch-process", component: BatchProcessComponent },
      { path: "address-master", component: AddressMastersComponent },
      {
        path: "create-address-master",
        component: AddressMasterCreateComponent,
      },
      {
        path: "create-handling-charges",
        component: CreateHandlingChargeComponent,
      },
      { path: "handling-charges", component: HandlingChargesComponent },
      { path: "system-configuration", component: SystemConfigurationComponent },
      { path: "systemConfigurationPreview", component: PreviewComponent },
      { path: "passwordPolicy", component: PasswordPolicyComponent },
      { path: "logViewer", component: LogViewerComponent },
      { path: "fastVoid", component: FastVoidComponent },
      { path: "dimentions", component: DimentionsComponent },
      { path: "accessorialRateKey", component: AccessorialRateKeyComponent },
      { path: "eflowLicenceViewer", component: EflowLicenceViewerComponent },
      { path: "customization", component: CustomizationComponent },
      { path: "ltlConfiguration", component: LtlConfigurationComponent },
      { path: "packingList", component: PackingListComponent },
      { path: "bolConfiguration", component: BolConfigurationComponent },
      {
        path: "createPackagingList",
        component: CreatePackagingListComponent,
      },
      {
        path: "createPackagingList/:id",
        component: CreatePackagingListComponent,
      },
      // Defaults
      { path: "", redirectTo: "process-shipping", pathMatch: "full" },

      //{ path: '**', component: ErrPageComponent }
    ],
  },
  { path: "login", component: LoginComponent },
  { path: "pop-up", component: PopUpComponent },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { enableTracing: true, useHash: false }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
