import { Component, OnInit } from '@angular/core';
import { PlantConfiguration } from '../plant-configuration-model';
import { Lookup } from 'src/app/functional-components/process-shipping/process-shipping.model';
import { ActivatedRoute, Router } from '@angular/router';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';
import { DataService } from 'src/app/services/data.service';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-create-plant-configuration',
  templateUrl: './create-plant-configuration.component.html',
  styleUrls: ['./create-plant-configuration.component.scss']
})
export class CreatePlantConfigurationComponent implements OnInit {

  plantData: PlantConfiguration= {};
  states:Lookup[];
  currencies:Lookup[];
  countries:Lookup[];
  logLevels:Lookup[]=
  [{textField:'Debug',valueField: 1},
  {textField:'Info',valueField: 'INFO'},
  {textField:'Warn',valueField: 'WARN'},
  {textField:'Exception',valueField: 'EXCEPTION'},
  {textField:'CriticalException',valueField: 'CRITICALEXCEPTION'},];
  weightMesurmentCodes=[{textField:'LBS',valueField: 'LBS'},{textField:'KGS',valueField: 'KGS'}];
  packageDimentions=[{textField:'IN',valueField: 'IN'},{textField:'CM',valueField: 'CM'}];
  IDIRSType=[{textField:'Employer ID Number',valueField:'Employer ID Number'}];
  id:number;
  breadcrumb='New'
  constructor(private activatedRoute: ActivatedRoute,private processShipperService: ProcessShipperService,
    private dataService: DataService,private router: Router) { }

  ngOnInit(): void {

    const statePromise = this.processShipperService.getStateData();
    const currencyPromise = this.processShipperService.getCurrencyData();
    const warehousePromise = this.processShipperService.getCountryMasterData();
    // forkJoin() to make parallel calls
    forkJoin([statePromise, currencyPromise, warehousePromise]).subscribe(responses => {
        let stateData = responses[0];
        let currenciesData = responses[1];
        let countryData = responses[2];

        this.countries = countryData
      .map(x => { return { textField: x.country, valueField: x.country } as Lookup });
      this.states = stateData
      .map(x => { return { textField: x.scode, valueField: x.scode } as Lookup });
      this.currencies = currenciesData
      .map(x => { return { textField: x.currencyCode1, valueField: x.currencyCode1 } as Lookup });
    });

    this.activatedRoute.params.subscribe(paramsId => {
      this.id = paramsId.id;
      console.log(this.id);
      if(this.id>0){
        this.breadcrumb='Update'
      this.processShipperService.getPlantConfigurationDatabyId(this.id).subscribe(plantData=>{
        console.log(plantData);
        this.plantData = plantData;
      })
    }else{
      this.plantData.uom='LBS';
      this.plantData.idirstype='Employer ID Number';
      this.plantData.loggingLevelDefinitionValueId=1;
      this.plantData.packageDimensionsUom="IN";
    }
    });

  }
  backToPlants(){
    this.router.navigate(['/plants/']);
  }

  savePlants(){
    if(this.id>0){
this.processShipperService.updatePlantConfiguration(this.plantData).subscribe(plantData=>{
  console.log(plantData);
});}else{
this.processShipperService.savePlantConfiguration(this.plantData).subscribe(plantData=>{
  console.log(plantData);
  this.backToPlants();
});

    }
  }

}

