import { Component, Input, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { eFlowClient } from "src/app/models/eflowClient.model";
import { DataService } from "src/app/services/data.service";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-eflow-configuration",
  templateUrl: "./eflow-configuration.component.html",
  styleUrls: ["./eflow-configuration.component.scss"],
})
export class EflowConfigurationComponent implements OnInit {
  @Input() data = [];
  id: number;
  eflowData: eFlowClient = {};
  clientEflows = [
    { textField: "Local", valueField: 1 },
    { textField: "Remote", valueField: 2 },
  ];
  showAddpopup = false;
  @Input() columns = [
    "Quick Actions",
    "Client Name",
    "Client Id",
    "Client Description",
    "Service URL",
    "Created Date",
  ];
  constructor(
    private processShipperService: ProcessShipperService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getEflowData();
  }

  getEflowData() {
    this.processShipperService
      .getEflowConfiguration()
      .subscribe((plantData) => {
        console.log(plantData);
        this.data = plantData;
      });
  }
  delete(id) {
    this.processShipperService.deleteEflowClient(id).subscribe((data) => {
      this.eflowData = data;
      this.showAddpopup = false;
      this.getEflowData();
      this.eflowData = {};
    });
  }
  create() {
    this.showAddpopup = true;
  }
  save() {
    this.eflowData.clientName = this.eflowData.clientId;
    this.processShipperService
      .saveEflowClient(this.eflowData)
      .subscribe((data) => {
        this.eflowData = data;
        this.getEflowData();
        this.showAddpopup = false;
        this.eflowData = {};
      });
  }

  unubscribe(id) {
    this.processShipperService
      .unsubscribeEflowClient(this.eflowData, id)
      .subscribe((data) => {
        this.eflowData = data;
        this.showAddpopup = false;
        this.eflowData = {};
      });
  }
}
