import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { Lookup } from "src/app/functional-components/process-shipping/process-shipping.model";
import { boxTypeConfigurationModel } from "src/app/models/boxTypeConfiguration.model";
import { BoxTypeConfigurationService } from "src/app/services/boxTypeConfiguration.service";
import { DataService } from "src/app/services/data.service";
import { ToolkitServiceService } from "src/app/services/toolkit-service.service";

@Component({
  selector: "app-create-box-type-configuration",
  templateUrl: "./create-box-type-configuration.component.html",
  styleUrls: ["./create-box-type-configuration.component.scss"],
})
export class CreateBoxTypeConfigurationComponent implements OnInit {
  plantList: Lookup[] = [];
  boxTypeObj: boxTypeConfigurationModel = {};
  id: number;
  constructor(
    private activatedRoute: ActivatedRoute,
    private boxTypeService: BoxTypeConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getPlantData();
    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;
      if (this.id > 0) {
        this.boxTypeService.getBoxType(this.id).subscribe((data) => {
          this.boxTypeObj = data;
        });
      }
    });
  }

  getPlantData() {
    this.boxTypeService.getPlantMasterData().subscribe((responses) => {
      console.log(responses);
      responses.forEach((x: any) => {
        this.plantList.push({
          valueField: x.id,
          textField: x.plantId.toUpperCase(),
        });
      });
    });
  }
  back() {
    this.router.navigate(["/boxTypeConfigurations/"]);
  }

  save() {
    if (this.id > 0) {
      this.boxTypeService.update(this.boxTypeObj).subscribe((data) => {
        console.log(data);
        this.router.navigate(["/boxTypeConfigurations/"]);
      });
    } else {
      this.boxTypeService.save(this.boxTypeObj).subscribe((data) => {
        console.log(data);
        this.router.navigate(["/boxTypeConfigurations/"]);
      });
    }
  }
}
