import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ShipViaCodeConfigurationModel } from "../models/shipviaCodeConfiguration.model";
import { GlobalService } from "./global.service";
import { DropDown } from "../models/special-services.model";
import {
  PrinterConfigurationModel,
  ScaleConfigModel,
} from "../models/printerConfiguration.model";
import { systemConfigurationModel } from "../models/systemConfiguration.model";
import { loggingModel } from "../models/logging.model";
import { customizationModel } from "../models/customization.model";
import { packingListConfigurationModel } from "../models/packingListConfiguration.model";

@Injectable({
  providedIn: "root",
})
export class configurationService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  getPackingList() {
    return this.http.get<any[]>(
      this.gs.baseURL + "api/MasterData/GetPackingList"
    );
  }

  getDefinationValue(code: string) {
    return this.http.get<any>(
      this.gs.baseURL + "api/MasterData/GetDefinationValue/" + code
    );
  }

  getPlantMasterData() {
    return this.http.get<DropDown[]>(
      this.gs.baseURL + "api/MasterData/GetPlants"
    );
  }

  getPrintConfigurationValue() {
    return this.http.get<PrinterConfigurationModel>(
      this.gs.baseURL + "api/Peripherals"
    );
  }
  savePrintConfiguration(data: PrinterConfigurationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(this.gs.baseURL + "api/Peripherals", data, {
      headers: headers,
    });
  }

  getSacleConfigurationValue() {
    return this.http.get<ScaleConfigModel>(this.gs.baseURL + "api/Peripherals");
  }
  saveScaleConfiguration(data: ScaleConfigModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/Peripherals/ScaleConfig",
      data,
      {
        headers: headers,
      }
    );
  }

  getSystemConfiguration() {
    return this.http.get<systemConfigurationModel[]>(
      this.gs.baseURL + "api/SystemConfiguration"
    );
  }

  getLogs(fromDate, toDate) {
    return this.http.get<loggingModel[]>(
      this.gs.baseURL + "api/Logging?fromDate=" + fromDate + "&toDate=" + toDate
    );
  }

  getCustomizationData(plantId) {
    return this.http.get<customizationModel>(
      this.gs.baseURL +
        "api/CustomizationConfiguration/GetCustomize_DelDoc/" +
        plantId
    );
  }

  saveCustomizationConfiguration(data: customizationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/CustomizationConfiguration",
      data,
      {
        headers: headers,
      }
    );
  }

  getPackingListConfiguration() {
    return this.http.get<packingListConfigurationModel[]>(
      this.gs.baseURL + "api/PackingListConfiguration"
    );
  }
  getPackingListConfigurationbyId(id) {
    return this.http.get<packingListConfigurationModel>(
      this.gs.baseURL + "api/PackingListConfiguration/" + id
    );
  }

  savePackingListConfiguration(data: packingListConfigurationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/PackingListConfiguration",
      data,
      {
        headers: headers,
      }
    );
  }

  updatePackingListConfiguration(data: packingListConfigurationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.put<any>(
      this.gs.baseURL + "api/PackingListConfiguration/" + data.id,
      data,
      { headers: headers }
    );
  }
}
