import { Component, OnInit } from "@angular/core";
import { configurationService } from "../../../services/configuration-service";
import { getAllPrinters } from "../../../javascriptfun.js";
import { PrinterConfigurationModel } from "src/app/models/printerConfiguration.model";

@Component({
  selector: "app-printer-setup",
  templateUrl: "./printer-setup.component.html",
  styleUrls: ["./printer-setup.component.scss"],
})
export class PrinterSetupComponent implements OnInit {
  filterSectionExpanded = false;
  SettingsAssociatedPlant = false;
  AustraliaPost = false;
  PrintModes = false;
  packingListType = [];
  availablePrinters = [];
  packingList = [
    { textField: "8.5X11 Packing List" },
    { textField: "4X6 Packing List" },
  ];
  genericPrinterType = [
    { textField: "Eltron", textValue: "EPL" },
    { textField: "Zebra", textValue: "ZPL" },
  ];

  dpdLocalPrinterType = [
    { textField: "Citizen", textValue: "CLP" },
    { textField: "Eltron", textValue: "EPL" },
    { textField: "HTML", textValue: "HTML" },
  ];

  fedexLabelType = [];

  glsLabelType = [
    { textField: "Paper Label", textValue: "PAPER_LABEL" },
    { textField: "PDF 4X5", textValue: "PDF_4X5LABEL" },
    { textField: "ZPL Short", textValue: "ZPL_SHORT_LABEL" },
    { textField: "ZPL Long", textValue: "ZPL_LONG_LABEL" },
    { textField: "ZPL Short PON", textValue: "ZPL_SHORT_LABEL_PON" },
    { textField: "ZPL Long PON", textValue: "ZPL_LONG_LABEL_PON" },
    { textField: "NO LABEL", textValue: "NO_LABEL" },
  ];
  plantList: any = [];
  australianPostValue: any = [];
  printerConfigurationModel: PrinterConfigurationModel = {};

  constructor(private configurationService: configurationService) {}

  ngOnInit(): void {
    this.configurationService.getPackingList().subscribe((data) => {
      this.packingListType = data;
    });
    this.configurationService.getDefinationValue("LT").subscribe((data) => {
      this.fedexLabelType = data;
    });

    this.configurationService.getPrintConfigurationValue().subscribe((data) => {
      this.printerConfigurationModel = data;
      this.printerConfigurationModel.bolInteraction =
        this.printerConfigurationModel.bolInteraction.trim();
      this.printerConfigurationModel.lableInteraction =
        this.printerConfigurationModel.lableInteraction.trim();
      this.printerConfigurationModel.invoiceInteraction =
        this.printerConfigurationModel.invoiceInteraction.trim();
      this.printerConfigurationModel.ltlinteraction =
        this.printerConfigurationModel.ltlinteraction.trim();
      this.printerConfigurationModel.cartonLabelInteraction = this
        .printerConfigurationModel.cartonLabelInteraction
        ? "Yes"
        : "No";
      this.printerConfigurationModel.op900interaction = this
        .printerConfigurationModel.op900interaction
        ? "Yes"
        : "No";
      this.printerConfigurationModel.packingListInt =
        this.printerConfigurationModel.packingListInt.trim();
      this.printerConfigurationModel.genericInteraction =
        this.printerConfigurationModel.genericInteraction.trim();
      this.printerConfigurationModel.packingSlipInt =
        this.printerConfigurationModel.packingSlipInt.trim();
      this.printerConfigurationModel.coointeraction =
        this.printerConfigurationModel.coointeraction.trim();
      this.printerConfigurationModel.dangerousGoodlabelInteraction = this
        .printerConfigurationModel.dangerousGoodlabelInteraction
        ? "Yes"
        : "No";
      this.printerConfigurationModel.naftaInteraction = this
        .printerConfigurationModel.naftaInteraction
        ? "Yes"
        : "No";
    });

    this.getPlantData();
    this.getAutralianPostddlValue();
    const availablePrinters = getAllPrinters().split(",");
    availablePrinters?.forEach((element) => {
      if (element) {
        this.availablePrinters.push({
          textField: element,
          valueField: element,
        });
      }
    });
  }

  getAutralianPostddlValue() {
    this.configurationService.getDefinationValue("AUS_PP").subscribe((data) => {
      data.forEach((element) => {
        this.australianPostValue.push({
          id: element.id,
          value: element.description,
        });
      });
      //this.australianPostValue = data;
      console.log(this.australianPostValue);
    });
  }

  getPlantData() {
    this.configurationService.getPlantMasterData().subscribe((responses) => {
      console.log(responses);
      responses.forEach((x: any) => {
        this.plantList.push({
          valueField: x.id,
          textField: x.plantId.toUpperCase(),
        });
      });
    });
  }
  save() {
    this.printerConfigurationModel.cartonLabelInteraction =
      this.printerConfigurationModel.cartonLabelInteraction == "Yes"
        ? true
        : false;
    this.printerConfigurationModel.op900interaction =
      this.printerConfigurationModel.op900interaction == "Yes" ? true : false;
    this.printerConfigurationModel.dangerousGoodlabelInteraction =
      this.printerConfigurationModel.dangerousGoodlabelInteraction == "Yes"
        ? true
        : false;
    this.printerConfigurationModel.naftaInteraction =
      this.printerConfigurationModel.naftaInteraction == "Yes" ? true : false;
    this.configurationService
      .savePrintConfiguration(this.printerConfigurationModel)
      .subscribe((response) => {
        console.log(response);
      });
  }

  makeDefaultPrinter() {
    this.printerConfigurationModel.labelPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.labelLtlprinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.labelGenericPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.reportsPrinters =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.bolprinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.packingSlipPrinters =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.genericTextPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.cartonLabelPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.op900printer =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.dhllabelPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.purolatorLabelPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.dpdlocalPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.glsprinterType =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.parcelPostPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.expressPostPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.startrackPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.internationalPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.startrackCourierPrinter =
      this.printerConfigurationModel.defaultPrinter;
    this.printerConfigurationModel.glsprinterType =
      this.printerConfigurationModel.defaultPrinter;
    // this.printerConfigurationModel.labelPrinter = this.printerConfigurationModel.defaultPrinter;
    // this.printerConfigurationModel.labelPrinter = this.printerConfigurationModel.defaultPrinter;
    // this.printerConfigurationModel.labelPrinter = this.printerConfigurationModel.defaultPrinter;
    // this.printerConfigurationModel.labelPrinter = this.printerConfigurationModel.defaultPrinter;
  }
}
