import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import { batchConfigurationDTOModel } from "../models/batchConfiguration.model";
import { BatchProcessModel } from "../models/batchProcess.model";

@Injectable({
  providedIn: "root",
})
export class BatchConfigurationService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  get() {
    return this.http.get<batchConfigurationDTOModel[]>(
      this.gs.baseURL + "api/BatchConfigurationDetail"
    );
  }

  getBatchConfigData(id) {
    return this.http.get<batchConfigurationDTOModel>(
      this.gs.baseURL + "api/BatchConfigurationDetail/" + id
    );
  }

  save(data: batchConfigurationDTOModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/BatchConfigurationDetail",
      data,
      {
        headers: headers,
      }
    );
  }

  getBpfileFields() {
    return this.http.get<batchConfigurationDTOModel[]>(
      this.gs.baseURL + "api/BatchConfigurationDetail/GetBpfileFields"
    );
  }

  getBatchProcess() {
    return this.http.get<BatchProcessModel>(
      this.gs.baseURL + "api/BatchProcess"
    );
  }
}
