import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';
import { GlobalService } from 'src/app/services/global.service';
import { ConfirmationService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { OverlayPanel } from 'primeng/overlaypanel';

@Component({
  selector: 'app-leftpanel',
  templateUrl: './leftpanel.component.html',
  styleUrls: ['./leftpanel.component.scss']
})
export class LeftpanelComponent implements OnInit, OnDestroy {

  @Output() leftPanelMenuItemClicked = new EventEmitter();
  @Input('menuCollapsed') menuCollapsed = true;
  @Output() menuCollapsedChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    public router: Router,
    public gs: GlobalService,
    public dataService: DataService,
    private confirmationService: ConfirmationService,
  ) { }

  menuitems: any;
  radiusServerVersion = '';
  filteredMDMMenus = [];
  selitem: any = "";
  MDMMenuFilter = "";
  masterMenuList = [];
  eventObj: any;
  protected subscription: Subscription = new Subscription();

  @ViewChild('menuOP') menuOP: OverlayPanel;
  selectedMenu;
  // routeSubscription: subscription;

  ngOnInit() {
     this.getMenuItems();
    this.subscription.add(
      this.dataService.menuAccessedObs.subscribe(res => {
        if (res) {
          this.getMenuItems();
          this.filteredMDMMenus = JSON.parse(JSON.stringify(this.dataService.MDMMenus));
          this.masterMenuList = [];
          this.filteredMDMMenus.forEach(m => {
            if (!m.children) {
              this.masterMenuList.push(m);
            } else {
              m.children.forEach(child => {
                this.masterMenuList.push(child);
              });
            }
          })
        }
      })
    );

    this.radiusServerVersion = localStorage.getItem('radiusServerVersion');
  }

  isActive(id) {
    let idToCheck = id.split('_');

    if (idToCheck.length == 1)
      return ((this.router.url.indexOf(id) > -1)) ? 'active menu-item-' + id : ' menu-item-' + id;
    else
      return ((this.router.url.indexOf(idToCheck[0]) > -1) || (this.router.url.indexOf(idToCheck[1]) > -1)) ? 'active menu-item-' + id : ' menu-item-' + id;
  }

  getMenuItems() {
    this.menuitems = JSON.parse(JSON.stringify(this.dataService.menuitems));

    this.menuitems.push(
      { id: "process-shipper", label: 'Process Shipper', icon: "fal fa-cog", path: "process-shipper", liClass: "process-shipper" }
    );

  }

  selectItem(item) {
    this.selitem = item;
    this.router.navigate([item]);
  }

  menuItemClicked(menuIcon) {
    this.leftPanelMenuItemClicked.emit(menuIcon);
  }

  hideMenu() {
    this.dataService.toggleSidebar(false);
  }

  menuClicked(item) {
    console.log(item);
    if (item.id === 'adminsettings') {
      this.dataService.pageChanged(3);
    }
  }

  goToComponent(component, event = null) {
    if (event) {
      event.stopPropagation();
      event.preventDefault();
      // this.menuOP.hide();
    }
    if (!component.children) {
      this.menuOP.hide();
      setTimeout(() => {
        this.router.navigate([component.path], { state: { context: component.context, additionalContext: component.additionalContext } });
      });
    
    }
  }

  openMDMLink(path, event) {
    event.stopPropagation();
    event.preventDefault();

    
  }

  showMenu(menuItem, event) {
    if (menuItem.children) {
      this.selectedMenu = menuItem;
      this.menuOP.toggle(event);
    }
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
