import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-eflow-licence-viewer',
  templateUrl: './eflow-licence-viewer.component.html',
  styleUrls: ['./eflow-licence-viewer.component.scss']
})
export class EflowLicenceViewerComponent implements OnInit {

  columns = [
    "Name",
    "Active",
    "licence Type",
    "Date Range",
  ];
  save(){}
  router: any;
  svcService: any;
  data: any;

  constructor() { }

  ngOnInit(): void {
    this.svcService.get().subscribe((data: any) => {
      this.data = data;
    });
  }
  edit(id: string) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }

}
