import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import {
  ShipViaCodeConfigurationModel,
  ShipviaCodeDetailDTO,
} from "../models/shipviaCodeConfiguration.model";
import { PlantDataModel } from "../functional-components/process-shipping/process-shipping.model";
import { PlantERPConfig } from "../functional-components/configuration/user-registration/user-registration-model";

@Injectable({
  providedIn: "root",
})
export class shipViaCodeConfigurationService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  get() {
    return this.http.get<ShipViaCodeConfigurationModel[]>(
      this.gs.baseURL + "api/ShipViaCodeConfiguration"
    );
  }

  getShipViaCodeData(id: any) {
    return this.http.get<ShipviaCodeDetailDTO>(
      this.gs.baseURL + "api/ShipViaCodeConfiguration/" + id
    );
  }

  getPlants() {
    return this.http.get<any[]>(this.gs.baseURL + "api/MasterData/GetPlants");
  }

  getPlantERP(plant: string) {
    return this.http.get<PlantERPConfig[]>(
      this.gs.baseURL + "api/MasterData/GetPlantERP/" + plant
    );
  }

  getPlantCarrier(plantId: string) {
    return this.http.get<ShipViaCodeConfigurationModel[]>(
      this.gs.baseURL + "api/MasterData/GetPlantCarrier/" + plantId
    );
  }

  getSignatureOption() {
    return this.http.get<any[]>(
      this.gs.baseURL + "api/MasterData/GetSignatureOption/"
    );
  }

  save(shipviaCodeDetailDTO: ShipviaCodeDetailDTO) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/ShipViaCodeConfiguration",
      shipviaCodeDetailDTO,
      {
        headers: headers,
      }
    );
  }
}
