  doFeatureSearch(event) {
    this.FeatureFilterValue = event.target.value;

    this.filteredFeatures = [];
    this.filteredFeatures = this.availableFeatures.filter((aFt) => {
      return (
        aFt["label"]
          .toLowerCase()
          .indexOf(this.FeatureFilterValue.toLowerCase()) > -1
      );
    });
    this.filteredFeatures.sort((a, b) => (a.label > b.label ? 1 : -1));
  }

  clearFeatureFilter() {
    this.FeatureFilterValue = "";
    this.filteredFeatures = JSON.parse(JSON.stringify(this.availableFeatures));
    this.filteredFeatures.sort((a, b) => (a.label > b.label ? 1 : -1));
  }

