import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { DataService } from "src/app/services/data.service";
import { configurationService } from "src/app/services/configuration-service";
import {
  ActiveMiscfield,
  CustomizeDelDoc,
  customizationModel,
} from "src/app/models/customization.model";
import { forEachChild } from "typescript";

@Component({
  selector: "app-customization",
  templateUrl: "./customization.component.html",
  styleUrls: ["./customization.component.scss"],
})
export class CustomizationComponent implements OnInit {
  administrationList = false;
  batchProcessList = false;
  configurationList = true;
  ERPSetupList = false;
  hazardousList = false;
  carriersIntegrationList = false;
  carriersNonIntegrationList = false;
  toolKit = false;
  plants: any;
  configurationData: CustomizeDelDoc = {
    name: "",
    packingtype: "",
    overPackShipment: "",
    phoneno: "",
    pono: "",
    invoiceNo: "",
    itemsScan: "",
    futureShip: "",
    customerCode: "",
    itemDescriptionEditing: false,
    delDocRequired: false,
    ponumRequired: false,
    invoiceNumRequired: false,
    internationalDetailsRequired: false,
    isbn: false,
    plantId: "",
    lockZipCodeAndState: false,
    handlingChargeOverride: false,
    handlingChargeCustomText: "",
    bestWayShipping: false,
    showCustomerId: false,
    useProductDescriptionForPackageDetails: false,
    returnServiceSwitchAddress: false,
    allowMultiShipVia: false,
    persistBoxTypes: false,
    allowSavingAddress: false,
    truncateShipToAddress: false,
    stopPoboxes: false,
    ltlpackageSummary: false,
    smallParcelPackageSummary: false,
    automaticReturnShipment: false,
    editShipment: false,
    scanDeldoc: false,
    delDocTrimLength: 0,
    persistPageField: false,
    persistWeightField: false,
    showPackageSequenceNumber: false,
    retainDelDocAcrossPlant: false,
    customReport: false,
    totalShipCost: false,
    autoReviewShip: false,
    enableDhlbbxshipment: false,
    motherShipmentValidity: 0,
    autoGenerateMotherShipId: 0,
    autoGenerateMotherPieceId: 0,
  };
  activeMiscFields: ActiveMiscfield[];

  data: customizationModel;

  constructor(
    private configurationService: configurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.configurationService.getPlantMasterData().subscribe((data) => {
      this.plants = data;
    });
  }
  plantChange(event) {
    this.configurationService
      .getCustomizationData(event.value)
      .subscribe((data) => {
        this.data = data;
        this.configurationData = this.data.customize_DelDoc;
        this.configurationData.futureShip == "Yes" ? true : false;
        this.configurationData.overPackShipment === "Yes" ? true : false;
        this.configurationData.itemsScan === "Yes" ? true : false;
        this.activeMiscFields = data.activeMiscfields;
        this.setActiveMiscField();
      });
  }
  setActiveMiscField() {
    if (!this.activeMiscFields) {
      this.activeMiscFields = [];
    }
    if (this.activeMiscFields && this.activeMiscFields.length < 10) {
      const loopCount = 10;

      for (let index: number = 0; index < loopCount; ) {
        index = index + 1;
        let miscObj = this.activeMiscFields.filter(
          (x) => x.miscfield == "MISC" + index.toString()
        )[0];
        if (miscObj) {
          miscObj.activeState = miscObj.activeState == "False" ? false : true;
          miscObj.miscstate = miscObj.miscstate == "False" ? false : true;
          continue;
        }
        const activeMiscField: ActiveMiscfield = {
          activeState: false,
          miscfield: "MISC" + index.toString(),
          miscstate: false,
          miscfieldValue: "",
          plantId: this.configurationData.plantId,
          printState: "False",
          userName: "",
        };
        this.activeMiscFields.push(activeMiscField);
      }

      this.activeMiscFields.sort(this.sorter);
    }
  }

  sorter(a, b) {
    return a.miscfield.localeCompare(b.miscfield, undefined, { numeric: true });
  }
  save() {
    this.configurationData.futureShip == true ? "Yes" : "No";
    this.configurationData.overPackShipment == true ? "Yes" : "No";
    this.configurationData.itemsScan == true ? "Yes" : "No";
    this.data.activeMiscfields = this.data.activeMiscfields.filter(
      (x) => x.miscfieldValue || x.activeState == true || x.miscstate == true
    );

    this.data.activeMiscfields.forEach((element) => {
      element.activeState = element.activeState == true ? "True" : "False";
      element.miscstate = element.miscstate == true ? "True" : "False";
    });
    this.configurationService
      .saveCustomizationConfiguration(this.data)
      .subscribe((data) => {
        console.log(data);
      });
  }
}
