import { Component, Input, OnInit } from "@angular/core";
import { DataService } from "src/app/services/data.service";
import { ToolkitServiceService } from "src/app/services/toolkit-service.service";
import { ToolKit } from "./tool-kit-configration.model";
import { Router } from "@angular/router";
@Component({
  selector: "app-tool-kit-configration",
  templateUrl: "./tool-kit-configration.component.html",
  styleUrls: ["./tool-kit-configration.component.scss"],
})
export class ToolKitConfigrationComponent implements OnInit {
  toolkitmodels: ToolKit[];

  @Input() columns = ["Quick Actions", "Code", "Description", "PlantID"];
  plantId = "Plant001";
  constructor(
    private toolkitservices: ToolkitServiceService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getToolkitdata();
  }
  getToolkitdata() {
    this.toolkitservices.getToolkitdata(this.plantId).subscribe((data) => {
      this.toolkitmodels = data;
      console.log(data);
    });
  }

  edit(id) {
    this.router.navigate(["/create-erp-configration/" + id]);
  }
  create() {
    this.router.navigate(["/create-erp-configration/"]);
  }
  delete(id) {
    this.toolkitservices.delete(id).subscribe((data) => {
      this.getToolkitdata();
    });
  }
}
