import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { BatchConfigurationService } from "src/app/services/batch-configuration.service";
import { DataService } from "src/app/services/data.service";
import { Lookup } from "../process-shipping/process-shipping.model";
import {
  EnquedBatch,
  BatchProcessDetail,
} from "../../models/batchProcess.model";

@Component({
  selector: "app-batch-process",
  templateUrl: "./batch-process.component.html",
  styleUrls: ["./batch-process.component.scss"],
})
export class BatchProcessComponent implements OnInit {
  data: any;
  columns = ["Quick Actions", "Del Doc", "Created Date"];
  col1 = ["Quick Actions", "Batch No", "Total Records", "Ship Date"];
  showAddpopup = true;
  templates = [];
  enquedShipData: EnquedBatch[];
  batchProcessData: BatchProcessDetail[];

  constructor(
    private bcService: BatchConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.bcService.getBatchProcess().subscribe((data) => {
      this.enquedShipData = data.enquedBatches;
      this.batchProcessData = data.batchProcessDetails;
    });

    this.bcService.get().subscribe((data) => {
      this.templates = data.map((x) => {
        return { textField: x.fileFormatName, valueField: x.id } as Lookup;
      });
    });
  }

  onUpload(event) {}
}
